import {
  AfterViewInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  Inject,
  Input,
  OnDestroy,
  OnInit,
  Output,
  QueryList,
  Renderer2,
  ViewChild,
  ViewChildren,
  ViewEncapsulation,
} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { BsDropdownDirective } from 'ngx-bootstrap/dropdown';
import { NgxSmartModalService } from 'ngx-smart-modal';
import { Subscription } from 'rxjs';

import { DOCUMENT } from '@angular/common';
import { filter } from 'rxjs/operators';
import {
  BlurEvent,
  ContextMenuComponent,
  DelayService,
  FieldDefinition,
  FieldDisplayMode,
  FolderTreeAction,
  Key,
  MetaData,
  MetaDataCollection,
  MetaDataDefinition,
  MetaDataType,
  MetaDataValidity,
  MetaDataValueType,
  RolePermissions,
  SaveLookupsEvent,
  StickyFieldsService,
} from 'shared-code-lib';
import { Action, ActionEvent } from '../../../../blocks/core/actions';
import { Filename } from '../../../../blocks/core/fileName';
import { Document as DocumentType } from '../../../../blocks/models/document';
import { FolderItem } from '../../../../blocks/models/folder-item';
import { LockStatus } from '../../../../blocks/models/lock-status';
import { IOutcome } from '../../../../blocks/models/outcome';
import { InteractiveWorkflowStep, WorkflowStep } from '../../../../blocks/models/workflow-step';
import { ActionService } from '../../../../blocks/services/action.service';
import { DragDropService } from '../../../../blocks/services/drag-drop.service';
import { ModalsUtilitiesService } from '../../../../blocks/services/modals-utilities.service';
import { Dictionary } from '../../../../blocks/services/models';
import { SpinnerStateService } from '../../../../blocks/services/spinner-state.service';
import { UserStateService } from '../../../../blocks/services/user-state.service';
import {
  Capabilities,
  DocumentListStyle,
  DragData,
  IndexingPanels,
  ValueLocation,
  ValueObj,
} from '../../capture-models';
import { CaptureConstants } from '../../capture-models/capture-constants';
import { IndexingPanelService } from '../../capture-services';
import { CaptureUpdateService } from '../../capture-services/capture-update.service';
import { DocumentCaptureService } from '../../capture-services/document-capture.service';
import { DocumentLastTouchedService } from '../../capture-services/document-last-touched.service';
import { DocumentListStyleService } from '../../capture-services/document-list-style.service';
import { DocumentLockingService } from '../../capture-services/document-locking.service';
import { FolderViewEventsService } from '../../capture-services/folder-view-events.service';
import { OutcomesService } from '../../capture-services/outcomes.service';
import { SelectedItemsService } from '../../capture-services/selected-items.service';

declare const $: any;

@Component({
  selector: 'app-document',
  templateUrl: './document.component.html',
  styleUrls: ['./document.component.scss'],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class DocumentComponent implements OnInit, OnDestroy, AfterViewInit {
  @ViewChild('docName') docName: ElementRef;
  @ViewChild('docWrapper', { static: true }) docWrapper: ElementRef;
  @ViewChild('wrapper') wrapper: ElementRef;
  @ViewChild('menu') menu: ElementRef;
  @ViewChildren('fieldCell', { read: ElementRef })
  fieldElements: QueryList<ElementRef>;

  @Input() folderItem: FolderItem;
  @Input() parentFolderId: string;
  @Input() doc: DocumentType;
  @Input() selected: boolean;
  @Input() stepMetadataDefinitions: Array<MetaDataDefinition> = [];
  @Input() stepMetadataDefinitionsFields: Array<FieldDefinition> = [];
  @Input() contextMenu: ContextMenuComponent;
  @Output() documentLoaded = new EventEmitter();

  isViewerLoaded: boolean;
  editing: boolean;
  lastDocumentName: string;
  subscriptions: Subscription;
  dragStarted: boolean;
  dropEnabled: boolean;
  overallValidity: MetaDataValidity;
  canDeleteDocument: boolean;
  canReplaceDocument: boolean;
  canRescanDocument: boolean;
  canManageWorkitem: boolean;
  hoverOver: boolean;
  hoverOverMenu: boolean;
  showMenu: boolean;
  dropup: boolean;
  FILENAME_CELL_INDEX: number = -1;
  selectedCellIndex: number;
  isGridView: boolean;
  DocumentGridPanel = IndexingPanels['DocumentGrid'];

  fieldDocumentName: FieldDefinition;
  fieldsValues = new Dictionary('', '');

  documentLocked: boolean;
  oneItemSelected: boolean;
  isDocumentSelected: boolean;
  multipleSelectedItems: boolean;

  documentString = CaptureConstants.document;

  outcomes: IOutcome[] = [];

  get metaDataValidity() {
    return MetaDataValidity;
  }

  constructor(
    private capture: DocumentCaptureService,
    private cdr: ChangeDetectorRef,
    private indexingService: IndexingPanelService,
    private userState: UserStateService,
    private delay: DelayService,
    private translate: TranslateService,
    private actionService: ActionService,
    private spinnerState: SpinnerStateService,
    private stickyService: StickyFieldsService,
    private captureUpdate: CaptureUpdateService,
    private documentListStyleService: DocumentListStyleService,
    private folderViewEvents: FolderViewEventsService,
    private documentLockingService: DocumentLockingService,
    public userStateService: UserStateService,
    private selectedItemsService: SelectedItemsService,
    private documentLastTouchedService: DocumentLastTouchedService,
    private dragDropService: DragDropService,
    private outcomesService: OutcomesService,
    private ngxSmartModalService: NgxSmartModalService,
    private renderer: Renderer2,
    private modalsUtilitiesService: ModalsUtilitiesService,
    @Inject(DOCUMENT) private document: Document
  ) {
    this.isViewerLoaded = false;
    this.editing = false;
    this.subscriptions = new Subscription();
    this.dragStarted = false;
    this.dropEnabled = false;
    this.documentLocked = true;
  }

  ngOnInit() {
    this.fieldDocumentName = new FieldDefinition({
      type: MetaDataType.Text,
      required: false,
      label: '',
    });

    this.lastDocumentName = this.doc.filename;

    this.subscriptions.add(this.documentLockingService.lockStatusChanged.subscribe(this.lockStatusChanged));

    this.isGridView = this.documentListStyleService.getStyle() === DocumentListStyle.Grid;
    this.updateFieldsDisplayMode();

    this.subscriptions.add(this.indexingService.onInit.subscribe(() => (this.isViewerLoaded = true)));
    this.subscriptions.add(this.actionService.onActivateAction.subscribe(this.invokeAction));

    this.documentLoaded.emit();

    this.renderer.listen(this.docWrapper.nativeElement, 'contextmenu', (event) => {
      if (!this.isDocumentSelected) {
        this.selectDocument();
      }

      this.contextMenu.open(event, null, null, this.getDocumentContextMenuActions());
    });

    this.subscriptions.add(
      this.indexingService.onChangeCurrentFieldValue.subscribe((valueObj: ValueObj) => {
        if (this.folderItem.workItem.id !== valueObj.updateFieldData.context.id) return;

        if (valueObj.panel !== IndexingPanels.LineItemIndexing) {
          this.setMetaDataValueAndLocation(valueObj);
        }

        if (this.isGridView && valueObj.panel === this.DocumentGridPanel && this.selected) {
          if (this.selectedCellIndex === this.FILENAME_CELL_INDEX) {
            this.onFilenameBlur(valueObj.updateFieldData.value);
          } else {
            const mdd = this.stepMetadataDefinitions[this.selectedCellIndex];
            if (mdd.type === MetaDataType.Expression) {
              this.moveToNextCell();
              return;
            }

            this.checkValues(valueObj.updateFieldData.value, mdd, this.selectedCellIndex);
            this.fieldsValues[mdd.name] = this.getFieldValue(mdd);
          }

          this.cdr.detectChanges();
          this.moveToNextCell();
        }
      })
    );

    this.subscriptions.add(
      this.captureUpdate.onChangeDocument.subscribe((doc) => {
        this.documentLocked = true; // assume positive locking cause we screw up focus otherwise
        this.selected = doc && doc.id === this.doc.id;
        if (this.selected && this.isGridView) {
          const valuesWithValidLocation: Array<ValueLocation> = [];

          this.stepMetadataDefinitions.forEach((field) => {
            const fieldId = field.id;
            const md = this.getWorkItemMetadata(field.name) as MetaData;
            if (md && md.location && md.location.valid) {
              const location = md.location;
              const validity = md.validity;
              valuesWithValidLocation.push({ fieldId, location, validity });
            }
          });

          this.captureUpdate.hasValuesWithValidLocation(valuesWithValidLocation);

          this.delay.execute(() => {
            if (!this.selectedCellIndex) {
              const lastTouchedField = this.getLastTouchedField();
              this.selectedCellIndex = lastTouchedField !== null ? lastTouchedField : -1;
            }

            this.updateCellUI(this.selectedCellIndex, true);
          }, 0);
        } else {
          this.selectedCellIndex = this.FILENAME_CELL_INDEX;
        }
      })
    );

    this.subscriptions.add(
      this.indexingService.onPointAndClickInitialized.subscribe(() => {
        if (this.isGridView && this.selected) {
          this.emitCurrentCellChange(this.getCurrentFieldName());
        }
      })
    );

    this.subscriptions.add(
      this.documentListStyleService.onChangeDocumentListStyle.subscribe((style: DocumentListStyle) => {
        this.isGridView = style === DocumentListStyle.Grid;
        if (this.isGridView) this.delay.execute(this.setGridCellsStyles, 0);
        this.updateFieldsValues();
        this.updateFieldsDisplayMode();
        this.cdr.detectChanges();
      })
    );

    this.subscriptions.add(
      this.folderViewEvents.onUpdateItem
        .pipe(filter((a) => a.workItem.id == this.folderItem.workItem.id))
        .subscribe((folderItem) => {
          this.folderItem = folderItem;
          this.overallValidity = folderItem.workItem.getOverallValidity(this.getCurrentWorkflowStep(), this.userState);
        })
    );

    this.onChangedSelectedItems();
    this.subscriptions.add(this.selectedItemsService.onChangeSelectedItems.subscribe(this.onChangedSelectedItems));

    this.subscriptions.add(this.dragDropService.onElementDragStarted.subscribe(this.elementDragStarted));

    this.subscriptions.add(
      this.dragDropService.onElementDragEnded.subscribe(() => {
        this.dragStarted = false;
      })
    );

    this.overallValidity = this.folderItem.workItem.getOverallValidity(this.getCurrentWorkflowStep(), this.userState);

    const ws: WorkflowStep = this.capture.getWorkflowStepById(this.folderItem.workItem.workflowStepId);
    if (ws) {
      if (ws.hasCapability(Capabilities.deleteDocument)) {
        this.canDeleteDocument = true;
      }

      if (ws.hasCapability(Capabilities.allowCaptureReimportPage)) {
        this.canReplaceDocument = true;
      }

      if (ws.hasCapability(Capabilities.allowCaptureRescanPage)) {
        this.canRescanDocument = true;
      }
    }

    this.canManageWorkitem = this.userState.hasRolePermission(RolePermissions.WORKITEM_MANAGE);

    this.subscriptions.add(
      this.captureUpdate.onWorkitemMetadataUpdated.subscribe((metadataCollection: MetaDataCollection) => {
        this.updateFieldsValues();
      })
    );

    this.updateFieldsValues();

    this.subscriptions.add(
      this.captureUpdate.onChangeOutcomeButtons.subscribe((outcomes) => {
        this.outcomes = outcomes;
        this.detectChanges();
      })
    );
  }

  getLastTouchedField() {
    const lastTouchedFieldId = this.documentLastTouchedService.getLastTouchedField();
    const lastTouchedDocumentId = this.documentLastTouchedService.getLastTouchedDocumentId();

    if (lastTouchedFieldId && lastTouchedDocumentId && lastTouchedDocumentId === this.doc.id) {
      this.documentLastTouchedService.clearLastTouchedField();
      this.documentLastTouchedService.clearLastTouchedDocumentId();

      return this.stepMetadataDefinitions.findIndex((mdd) => mdd.id === lastTouchedFieldId);
    }

    return null;
  }

  lockStatusChanged = (ls: LockStatus): void => {
    if (!this.folderItem || !this.folderItem.workItem) return;

    if (ls.id == this.folderItem.workItem.id) {
      this.documentLocked = ls.locked && ls.lockedByUserId == this.userStateService.user.id && !ls.expired();
    }

    this.cdr.detectChanges();
  };

  getDocumentContextMenuActions = (): Array<FolderTreeAction> => {
    const actions: Array<FolderTreeAction> = [];
    if (!this.isGridView && this.oneItemSelected) {
      actions.push({
        name: this.translate.instant('document-actions.rename'),
        action: () => {
          this.editDocumentName();
        },
        disabled: !this.documentLocked,
      });
    }

    if (this.canDeleteDocument) {
      actions.push({
        name: this.translate.instant('document-actions.delete'),
        action: () => {
          this.deleteSelectedItems();
        },
        disabled: !this.documentLocked && !this.multipleSelectedItems,
      });
    }

    if (this.canReplaceDocument && this.oneItemSelected) {
      actions.push({
        name: this.translate.instant('document-actions.replace-document'),
        action: () => {
          this.replace();
        },
        disabled: !this.documentLocked,
      });
    }

    if (this.canRescanDocument && this.oneItemSelected) {
      actions.push({
        name: this.translate.instant('document-actions.rescan-document'),
        action: () => {
          this.rescan();
        },
        disabled: !this.documentLocked,
      });
    }

    if (this.canManageWorkitem && this.oneItemSelected) {
      actions.push({
        name: this.translate.instant('document-actions.properties'),
        action: () => {
          this.showWorkitemProperties();
        },
      });
    }

    this.outcomes.forEach((outcome: IOutcome) => {
      if (outcome.enabled) {
        actions.push({
          name: outcome.label,
          action: () => {
            this.capture.executeOutcome(outcome);
          },
          disabled: !this.documentLocked || !this.isDocumentSelected,
        });
      }
    });

    return actions;
  };

  onChangedSelectedItems = () => {
    this.oneItemSelected = this.selectedItemsService.isOneItemSelected();
    this.isDocumentSelected = this.selectedItemsService.isItemSelected(
      this.folderItem.workItem.id,
      this.folderItem.workItem.workflowStepId
    );
    this.multipleSelectedItems = this.selectedItemsService.areMultipleItemsSelected();
  };

  elementDragStarted = () => {
    if (this.isDocumentSelected) {
      this.dragStarted = true;

      if (!this.dragDropService.isItemDragged(this.folderItem.workItem.id) && this.multipleSelectedItems) {
        const dragData: DragData = {
          element: this.folderItem.workItem,
          elementParentId: this.parentFolderId,
          elementParentStepId: this.folderItem.workItem.workflowStepId,
          elementType: CaptureConstants.document,
          document: this.doc,
        };
        this.dragDropService.pushDragData(dragData);
      }
    }
  };

  ngAfterViewInit() {
    this.setGridCellsStyles();
    this.focusLastTouchedField();
  }

  setGridCellsStyles = () => {
    const headerCells = (this.document.getElementById('grid-header-row') as HTMLTableRowElement).children;
    const dataCells = (this.document.getElementById('grid-data-row') as HTMLTableRowElement).children;

    for (let i = 0; i < headerCells.length - 1; i++) {
      const width = headerCells.item(i).getBoundingClientRect().width.toFixed(2);
      (dataCells.item(i) as HTMLElement).style.width = width + 'px';
      (headerCells.item(i) as HTMLElement).style.width = width + 'px';
    }
  };

  focusLastTouchedField() {
    if (!this.isGridView) return;

    const lastTouchedField = this.getLastTouchedField();
    if (lastTouchedField === null) return;

    this.delay.execute(() => {
      this.updateCellUI(lastTouchedField, false);
    }, 100);
  }

  updateFieldsValues() {
    if (!this.isGridView) return;

    this.stepMetadataDefinitions.forEach((mdd) => {
      this.fieldsValues[mdd.name] = this.getFieldValue(mdd);
    });
  }

  updateFieldsDisplayMode() {
    this.stepMetadataDefinitionsFields.forEach(
      (x) => (x.displayMode = this.isGridView ? FieldDisplayMode.grid : FieldDisplayMode.vertical)
    );
  }

  invokeAction = (event: ActionEvent): void => {
    if (!this.selected) return;

    if (event.action === Action.EditSelection) {
      if (!this.documentLocked) return;

      this.editDocumentName();
    } else if (event.action === Action.DeleteSelectedItem || event.action === Action.CtrlDeleteSelectedItem) {
      if (!this.documentLocked || !this.canDeleteDocument || this.modalsUtilitiesService.isAnyModalOpened()) return;

      this.deleteSelectedItems();
    }
  };

  getCurrentWorkflowStep(): InteractiveWorkflowStep {
    return this.capture.getWorkflowStepById(this.folderItem.workItem.workflowStepId) as InteractiveWorkflowStep;
  }

  ngOnDestroy() {
    this.subscriptions.unsubscribe();
  }

  selectDocument(event?: MouseEvent): void {
    if (event) {
      const target = event.target as HTMLElement;
      if (target && target.className === 'icomoon-more') return;
    }

    this.capture.selectDocument(
      this.doc,
      this.folderItem.workItem,
      this.parentFolderId,
      null,
      null,
      event?.ctrlKey || false
    );
  }

  get canDragDocuments(): boolean {
    const ws = this.capture.getWorkflowStepById(this.folderItem.workItem.workflowStepId);

    return !this.editing && ws.hasCapability(Capabilities.allowDropDocument);
  }

  isFilenameFocused() {
    return this.selected && this.selectedCellIndex === this.FILENAME_CELL_INDEX;
  }

  edit(e): void {
    e.stopPropagation();
    this.hide();
    this.editDocumentName();
  }

  editDocumentName = (): void => {
    this.editing = true;
    this.docName.nativeElement.removeAttribute('readonly');
    this.docName.nativeElement.removeAttribute('hidden');
    this.docName.nativeElement.value = '';

    this.delay.execute(() => {
      const strLength = this.docName.nativeElement.value.length;
      if (this.docName.nativeElement.setSelectionRange != undefined) {
        this.docName.nativeElement.setSelectionRange(strLength, strLength);
      }

      this.docName.nativeElement.value = this.doc.filename;
      this.docName.nativeElement.focus();
    }, 10);

    this.cdr.detectChanges();
  };

  onBlur() {
    setTimeout(() => {
      if (this.editing) {
        this.cancel();
      }
    }, 200);
  }

  onClick(e: any) {
    if (!this.editing) {
      this.docName.nativeElement.blur();
      this.actionService.activateAction(new ActionEvent(Action.SetFocusTreePanel));
    } else {
      e.stopPropagation();
    }
  }

  save(e?: Event): void {
    e?.stopPropagation();

    this.editing = false;
    this.docName.nativeElement.setAttribute('readonly', 'readonly');
    this.docName.nativeElement.setAttribute('hidden', 'hidden');

    this.lastDocumentName = this.doc.filename;
    this.doc.filename = this.docName.nativeElement.value;

    this.capture
      .editDocumentName(this.doc.id, this.doc.fileType, this.docName.nativeElement.value, this.folderItem.workItem.id)
      .then((newDocument: DocumentType) => {
        this.doc = newDocument;
        this.lastDocumentName = this.doc.filename;

        if (this.doc.id === this.capture.getCurrentDocument().id) {
          this.capture.updateDocumentName(this.doc.filename);
        }

        this.actionService.activateAction(new ActionEvent(Action.SetFocusTreePanel));
      })
      .catch((err: any) => this.cancel());
  }

  cancel(e?: Event): void {
    e?.stopPropagation();

    this.editing = false;
    this.docName.nativeElement.setAttribute('readonly', 'readonly');
    this.docName.nativeElement.setAttribute('hidden', 'hidden');
    this.docName.nativeElement.value = this.doc.filename;
    this.docName.nativeElement.blur();

    this.doc.filename = this.lastDocumentName;

    this.cdr.detectChanges();
  }

  checkValidity(e: KeyboardEvent) {
    if (!e) return;

    if (e.key === Key.Escape) {
      this.cancel();
      this.docName.nativeElement.blur();
    } else if (e.key === Key.Enter) {
      this.save();
      this.docName.nativeElement.blur();
    }
  }

  onDrop(dragData: DragData) {
    if (dragData instanceof DragEvent || !this.dropEnabled) return;

    if (dragData.elementType === CaptureConstants.page) this.dropPageIntoDocument(dragData);

    if (dragData.elementType === CaptureConstants.document) this.dropWorkItemsIntoDocument();

    this.dropEnabled = false;
  }

  dropPageIntoDocument(dragData: DragData) {
    this.onToggleSpinner(true);

    this.capture
      .handleDragDropPageIntoDocument(dragData, this.folderItem.workItem, this.doc)
      .then(() => {
        this.onToggleSpinner(false);
      })
      .catch(() => {
        this.onToggleSpinner(false);
      });
  }

  dropWorkItemsIntoDocument() {
    this.onToggleSpinner(true);

    this.capture
      .handleDragDropWorkItemsIntoDocument(
        this.dragDropService.dragDataArray,
        this.folderItem.workItem,
        this.doc,
        this.parentFolderId
      )
      .then(() => {
        this.dragDropService.clearDragData();
        this.onToggleSpinner(false);
      })
      .catch(() => {
        this.dragDropService.clearDragData();
        this.onToggleSpinner(false);
      });
  }

  onDragOver(dragData: DragData) {
    if (
      dragData &&
      dragData.elementParentStepId === this.folderItem.workItem.workflowStepId &&
      (this.isPageDragged(dragData) || this.isDocumentDragged(dragData))
    ) {
      this.dropEnabled = true;
    }
  }

  isPageDragged(dragData: DragData): boolean {
    return dragData.elementType === CaptureConstants.page && dragData.elementParentId !== this.doc.id;
  }

  isDocumentDragged(dragData: DragData): boolean {
    return (
      dragData.elementType === CaptureConstants.document &&
      !this.dragDropService.isItemDragged(this.folderItem.workItem.id)
    );
  }

  onDragLeave(dragData: DragData) {
    this.dropEnabled = false;
  }

  getPageCountText = (): string => {
    if (!this.doc) return '';

    return Filename.getPageCountText(this.doc.pageCount, this.translate);
  };

  onToggleSpinner(state: boolean) {
    if (state) {
      this.translate
        .get('Document_Capture.UpdatingDocument')
        .subscribe((value) => this.spinnerState.show(value, document.getElementById('app-spinner-panel')));
    } else {
      this.spinnerState.hide();
    }
  }

  onMouseEnter(isMenu?: boolean) {
    if (isMenu) {
      this.hoverOverMenu = true;
    } else {
      this.hoverOver = true;
    }

    this.cdr.detectChanges();
  }

  onMouseLeave(isMenu?: boolean) {
    this.delay.execute(() => {
      if (isMenu) {
        this.hoverOverMenu = false;
      } else {
        this.hoverOver = false;
      }

      this.cdr.detectChanges();
    }, 10);
  }

  onClickMenuButton(event: MouseEvent, dropdown: BsDropdownDirective) {
    if (!this.multipleSelectedItems || !this.isDocumentSelected) {
      this.capture.selectDocument(this.doc, this.folderItem.workItem, this.parentFolderId, null, null, event.ctrlKey);
    }

    this.contextMenu.open(event, null, null, this.getDocumentContextMenuActions());
  }

  replace() {
    this.hide();
    const result = this.capture.findFolder(this.parentFolderId).findDocument(this.doc.id);
    this.actionService.activateAction({ action: Action.ReimportDocument, documentResult: result });
  }

  rescan() {
    this.hide();
    const result = this.capture.findFolder(this.parentFolderId).findDocument(this.doc.id);
    this.actionService.activateAction({ action: Action.RescanDocument, documentResult: result });
  }

  hide() {
    this.hoverOver = false;
    this.hoverOverMenu = false;
  }

  onHidden() {
    this.showMenu = false;
  }

  getFieldValue(mdd: MetaDataDefinition): string {
    const metadata = this.getWorkItemMetadata(mdd.name);

    if (!metadata || metadata.valueType !== MetaDataValueType.String) {
      return '';
    }

    if (!mdd.stickyField) return <string>metadata.value;

    if (metadata.value) {
      this.stickyService.setStickyField(mdd.name, <string>metadata.value);
      return <string>metadata.value;
    }

    const stickyValue = this.stickyService.getStickyField(mdd.name);
    if (!stickyValue) return '';

    this.capture.validateAndSaveIndexingValue(stickyValue, mdd, this.folderItem.workItem);
    return stickyValue;
  }

  getValidity(mdd: MetaDataDefinition) {
    const metadata = this.getWorkItemMetadata(mdd.name);
    if (metadata) {
      return metadata.validity || MetaDataValidity.unknown;
    }
  }

  onFocusCell(cellIndex: number, mdd?: MetaDataDefinition) {
    this.updateCellUI(cellIndex, true);
    this.scrollToCell(cellIndex + 1);
  }

  onFocusFilenameCell(cellIndex: number) {
    this.updateCellUI(cellIndex, true);
    this.scrollToCell(0);
  }

  moveToNextCell() {
    if (this.selectedCellIndex === this.stepMetadataDefinitions.length - 1) {
      this.captureUpdate.selectNextDocument();
    } else {
      this.selectedCellIndex += 1;
      this.delay.execute(() => {
        this.updateCellUI(this.selectedCellIndex, true);
      }, 0);
    }
  }

  moveToPreviousCell() {
    if (this.selectedCellIndex > this.FILENAME_CELL_INDEX) {
      this.selectedCellIndex -= 1;
      this.updateCellUI(this.selectedCellIndex, true);
    }
  }

  emitCurrentCellChange(fieldLabelText: string) {
    this.indexingService.changeCurrentField(this.DocumentGridPanel, fieldLabelText);
  }

  onCellBlur(event: BlurEvent, mdd: MetaDataDefinition): void {
    if (event.context == null) return;
    if (event.mdd) mdd = event.mdd;

    this.capture.validateAndSaveIndexingValue(event.value, mdd, event.context);

    if (mdd.stickyField) {
      this.stickyService.setStickyField(mdd.name, event.value);
    }
  }

  onCellTab(event: BlurEvent, mdd: MetaDataDefinition, index: number) {
    if (index === this.stepMetadataDefinitions.length - 1) {
      this.onCellBlur(event, mdd);
    }
  }

  onCellTabKey(event: Event) {
    if (event) {
      event.preventDefault();
    }

    this.moveToNextCell();
  }

  onCellShiftTabKey(event: Event) {
    if (event) {
      event.preventDefault();
    }

    this.moveToPreviousCell();
  }

  onFilenameBlur(value: string) {
    this.saveFilename(value);
  }

  checkValues(value: any, mdd: MetaDataDefinition, itemIndex: number) {
    if (!this.capture.getCurrentWorkItem()) {
      return;
    }

    if (!this.folderItem) {
      return;
    }

    const prevValue = this.getFieldValue(mdd);
    if (value !== prevValue) {
      this.capture.validateAndSaveIndexingValue(value, mdd, this.folderItem.workItem);

      if (mdd.stickyField) {
        this.stickyService.setStickyField(mdd.name, value);
      }
    }
  }

  getWorkItemMetadata(name: string) {
    return this.folderItem.workItem.getMetaData(name);
  }

  updateCellUI(cellIndex: number, zoomIntoField: boolean) {
    this.updateCellIndex(cellIndex);
    this.zoomIntoViewerField(cellIndex, zoomIntoField);
  }

  zoomIntoViewerField(cellIndex: number, zoomIntoField: boolean) {
    let zoomInFieldId = null;
    if (zoomIntoField && cellIndex > this.FILENAME_CELL_INDEX) {
      const md = this.stepMetadataDefinitions[cellIndex];
      const metaData = this.getWorkItemMetadata(md.name);
      if (metaData) {
        const location = metaData.location;
        if (location && location.valid) {
          zoomInFieldId = md.id;
        }
      }
    }

    this.captureUpdate.zoomIntoValue(zoomInFieldId);
    this.cdr.detectChanges();
  }

  updateCellIndex(cellIndex: number) {
    this.selectedCellIndex = cellIndex;
    this.emitCurrentCellChange(this.getCurrentFieldName());
    this.documentLastTouchedService.setLastTouchedField(
      cellIndex > this.FILENAME_CELL_INDEX ? this.stepMetadataDefinitions[cellIndex].id : null
    );
    this.documentLastTouchedService.setLastTouchedDocumentId(this.doc.id);
    this.cdr.detectChanges();
  }

  getCurrentFieldName() {
    return this.selectedCellIndex === this.FILENAME_CELL_INDEX
      ? (this.translate.instant('folder.DocumentNameColumn') as string)
      : this.stepMetadataDefinitions[this.selectedCellIndex]?.name;
  }

  onSaveLookupValues(event: SaveLookupsEvent, index: number) {
    this.capture.updateMetadataValues(event.context, event.lookups);

    if (event.move) {
      if (event.forward) {
        this.moveToNextCell();
      } else {
        this.moveToPreviousCell();
      }
    }

    this.updateFieldsValues();
    this.cdr.detectChanges();
  }

  onCellReturn(value: any, mdd: MetaDataDefinition) {
    this.capture.validateAndSaveIndexingValue(value, mdd, this.folderItem.workItem);
  }

  onFilenameReturn(value: string) {
    this.saveFilename(value);
  }

  saveFilename(value: any) {
    if (value === this.doc.filename) return;

    this.capture
      .editDocumentName(this.doc.id, this.doc.fileType, value, this.folderItem.workItem.id)
      .then((newDocument: DocumentType) => {
        this.doc = newDocument;

        if (this.doc.id === this.capture.getCurrentDocument().id) {
          this.capture.updateDocumentName(this.doc.filename);
        }
      });
  }

  scrollToCell(cellIndex: number) {
    const cellField = this.getCellElement(cellIndex);
    if (!cellField) return;

    const workflowPanel = $('#workflow-panel');
    const cellElement = $(cellField.nativeElement);
    const scrollLeft = workflowPanel.scrollLeft();
    const panelWidth = workflowPanel.width();
    const elementOffsetLeft = cellElement.offset().left;
    const elementLeft = cellField.nativeElement.getBoundingClientRect().left;
    const elementRight = elementOffsetLeft + cellElement.width();
    const scrollOffset = 72;
    let cellOffset: number = elementRight - scrollOffset - panelWidth;

    const leftOffset = elementLeft - scrollOffset;

    if (cellIndex === 0) {
      workflowPanel.scrollLeft(0);
    } else if (leftOffset < 0 || cellOffset > 0) {
      if (cellIndex === this.fieldElements.length - 1) {
        const menuAreaWidth: number = 30;
        cellOffset += menuAreaWidth;
      }

      workflowPanel.scrollLeft(scrollLeft + cellOffset);
    }
  }

  getCellElement(cellIndex: number) {
    if (!this.fieldElements || cellIndex >= this.fieldElements.length) return null;

    const fields = this.fieldElements.toArray();
    if (!fields || fields.length === 0) return null;

    return fields[cellIndex];
  }

  onValueChanged(value: any, mdd: MetaDataDefinition) {
    this.fieldsValues[mdd.name] = value;
  }

  deleteSelectedItems = (): void => {
    this.outcomesService.confirmDeleteSelectedItems(this.confirmedDeleteSelectedItems);
  };

  confirmedDeleteSelectedItems = (data: any) => {
    this.capture.transitionSelectedItems(data.id);
  };

  onDragStarted() {
    this.dragStarted = true;
    this.dragDropService.elementDragStarted();
  }

  onDragEnded() {
    this.dragStarted = false;
    this.dragDropService.elementDragEnded();
  }

  showWorkitemProperties = () => {
    this.ngxSmartModalService.getModal('workitemProperties').open();
  };

  handleAction(action: Function) {
    this.showMenu = false;
    action();
  }

  setMetaDataValueAndLocation(valueObj: ValueObj) {
    const mdd = this.userStateService.user.metaData.find((x) => x.name == this.indexingService.getCurrentFieldName());

    if (mdd) {
      this.capture
        .getCurrentWorkItem()
        .setMetaData(
          mdd.name,
          valueObj.updateFieldData.value,
          MetaDataValidity.valid,
          valueObj.updateFieldData.location
        );

      this.capture.validateAndSaveIndexingValue(
        valueObj.updateFieldData.value,
        mdd as MetaDataDefinition,
        this.capture.getCurrentWorkItem(),
        true
      );
    }
  }

  detectChanges = (): void => {
    if (!this.cdr['destroyed']) {
      this.cdr.detectChanges();
    }
  };
}
