import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  EventEmitter,
  OnDestroy,
  OnInit,
  Output,
  ViewChild,
  ViewEncapsulation,
  isDevMode,
} from '@angular/core';

import { TranslateService } from '@ngx-translate/core';
import { saveAs } from 'file-saver/FileSaver';
import { PerfectScrollbarDirective } from 'ngx-perfect-scrollbar-portable';
import { ToastrService } from 'ngx-toastr';

import { NgxSmartModalComponent, NgxSmartModalService } from 'ngx-smart-modal';
import { Subscription } from 'rxjs';
import {
  Branding,
  CalculationsService,
  FieldDefinition,
  FixedDataSource,
  IFieldOptions,
  MetaDataType,
  RolePermissions,
  UserPreferencesService,
} from 'shared-code-lib';
import { ICapture } from '../../../blocks/components/document-scanner/models/scanner';
import { createPDFFromImages } from '../../../blocks/components/document-scanner/utils/pdf';
import { ISplitButtonItem } from '../../../blocks/components/split-button/split-item';
import { Action, ActionEvent, FolderActionEvent } from '../../../blocks/core/actions';
import { Filename } from '../../../blocks/core/fileName';
import { KeyboardActionMapping } from '../../../blocks/core/keyboardActionMapping';
import { Log } from '../../../blocks/core/log';
import { IDocument } from '../../../blocks/models/document';
import { LockStatus } from '../../../blocks/models/lock-status';
import { IOutcome } from '../../../blocks/models/outcome';
import { WorkItem } from '../../../blocks/models/work-item';
import { Workflow } from '../../../blocks/models/workflow';
import { WorkflowFolder } from '../../../blocks/models/workflow-folder';
import { WorkflowStep } from '../../../blocks/models/workflow-step';
import { ActionService } from '../../../blocks/services/action.service';
import { ErrorsService } from '../../../blocks/services/errors.service';
import { HydraApiService } from '../../../blocks/services/hydra-api.service';
import { InProgressService } from '../../../blocks/services/in-progress.service';
import { SpinnerStateService } from '../../../blocks/services/spinner-state.service';
import { UserStateService } from '../../../blocks/services/user-state.service';
import { Capabilities, Page } from '../capture-models';
import { CapturedDocument } from '../capture-models/document';
import {
  DocumentProperties,
  RescanDocument,
  RescanPage,
  ScanAdditionalPages,
  ScanOptions,
  ScanPages,
  ScanToFolder,
  ScanToWorkflow,
} from '../capture-models/scan-commands';
import { CaptureUpdateService } from '../capture-services/capture-update.service';
import { DocumentCaptureService } from '../capture-services/document-capture.service';
import { DocumentLockingService } from '../capture-services/document-locking.service';
import { SelectedItemsService } from '../capture-services/selected-items.service';

declare const $: any;

type ImporterCallback = (files: File[]) => void;

@Component({
  selector: 'app-toolbar',
  templateUrl: './toolbar.component.html',
  styleUrls: ['./toolbar.component.scss'],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ToolbarComponent implements OnInit, OnDestroy {
  @ViewChild(PerfectScrollbarDirective, { static: true }) directiveRef?: PerfectScrollbarDirective;
  @Output() scan = new EventEmitter<ScanPages>();
  @Output() discardDocuments = new EventEmitter();
  destinations: Array<Workflow>;
  selectedWorkflow: Workflow;
  currentFolder: WorkflowFolder;
  outcomeButtons: Array<IOutcome>;
  currentDocument: CapturedDocument;
  currentPage: Page;
  subscriptions: Subscription;
  scanSplitItems: Array<ISplitButtonItem> = [];
  importSplitItems: Array<ISplitButtonItem> = [];
  captureSplitItems: Array<ISplitButtonItem> = [];
  classNamePrefix = 'project-item-';

  documentLocked: boolean;
  oneItemSelected: boolean;
  hasSelectedItems: boolean;

  showScanner: boolean = false;
  isCameraAvailable: boolean = false;
  isProfileAvailable: boolean = true;

  invokedAction: Action = null;

  readonly LastSelectedWorfklowId = 'LastSelectedWorkflowId';

  readonly supportedFileTypes = Filename.supportedFileTypes;

  captureModal: NgxSmartModalComponent;

  fieldWorkflow: FieldDefinition;

  workflowDataSource = new FixedDataSource(new Array<IFieldOptions>());

  selectedWorkflowId: string;

  Action = Action;

  //note: folderEvents solves a chicken-egg problem - do not remove
  constructor(
    public captureService: DocumentCaptureService,
    private toastr: ToastrService,
    private translate: TranslateService,
    private captureUpdate: CaptureUpdateService,
    private cdr: ChangeDetectorRef,
    public ngxSmartModalService: NgxSmartModalService,
    private userState: UserStateService,
    private inProgress: InProgressService,
    private actionService: ActionService,
    private hydraApi: HydraApiService,
    private spinner: SpinnerStateService,
    private documentLockingService: DocumentLockingService,
    private userStateService: UserStateService,
    private calculations: CalculationsService,
    private selectedItemsService: SelectedItemsService,
    private userPreferencesService: UserPreferencesService,
    private ngxSmartModal: NgxSmartModalService,
    private errorService: ErrorsService
  ) {
    this.subscriptions = new Subscription();
  }

  ngOnInit() {
    this.subscriptions.add(this.documentLockingService.lockStatusChanged.subscribe(this.lockStatusChanged));

    this.captureService
      .loadWorkFlows()
      .then((workflows: Array<Workflow>) => this.workflowsLoaded(workflows))
      .catch((err: any) => this.loadWorkflowsFailed(err));

    this.subscriptions.add(this.actionService.onActivateAction.subscribe(this.invokeAction));

    this.subscriptions.add(
      this.captureUpdate.onChangeFolder.subscribe((folder) => {
        this.currentFolder = folder;
        this.updateUI();
      })
    );

    this.subscriptions.add(
      this.captureUpdate.onChangeDocument.subscribe((doc) => {
        this.documentLocked = false;
        this.currentDocument = doc;
        this.updateUI();
      })
    );

    this.subscriptions.add(this.captureUpdate.onParentFolderChanged.subscribe((doc) => (this.currentDocument = doc)));

    this.subscriptions.add(
      this.captureUpdate.onChangeDocumentPage.subscribe((page) => {
        this.currentPage = page;
        this.updateUI();
      })
    );

    this.subscriptions.add(
      this.captureUpdate.onChangeOutcomeButtons.subscribe((outcomes) => {
        this.outcomeButtons = this.updateOutcomesKeyboardMapping(outcomes);
        this.detectChanges();
      })
    );

    this.onChangedSelectedItems();
    this.subscriptions.add(this.selectedItemsService.onChangeSelectedItems.subscribe(this.onChangedSelectedItems));
    this.setCameraAvailability();
  }

  setCameraAvailability() {
    if (isDevMode) {
      this.isCameraAvailable = true;
      return;
    }

    navigator.mediaDevices
      .getUserMedia({
        video: true,
      })
      .then(() => {
        this.isCameraAvailable = true;
      })
      .catch(() => {
        this.isCameraAvailable = false;
      });
  }

  onChangedSelectedItems = () => {
    this.oneItemSelected = this.selectedItemsService.isOneItemSelected();
    this.hasSelectedItems = this.selectedItemsService.hasItemsSelected();
    this.updateUI();
  };

  lockStatusChanged = (ls: LockStatus): void => {
    if (!this.currentDocument || !this.currentDocument.workitem) return;

    if (ls.id == this.currentDocument.workitem.id) {
      this.documentLocked = ls.locked && ls.lockedByUserId == this.userStateService.user.id && !ls.expired();
      this.updateUI();
    }
  };

  updateOutcomesKeyboardMapping(outcomes: IOutcome[]): IOutcome[] {
    for (let i = outcomes.length - 1; i >= 0; i--) {
      const outcomeNumber = outcomes.length - i;

      const action = this.getOutcomeAction(outcomeNumber);
      if (!action) continue;

      const mapping = KeyboardActionMapping.getTooltipFor(action, this.translate);
      outcomes[i].label += `${mapping}`;
    }

    return outcomes;
  }

  getOutcomeAction(outcomeNumber: number): Action {
    if (outcomeNumber === 1) return Action.Outcome1;
    else if (outcomeNumber === 2) return Action.Outcome2;
    else if (outcomeNumber === 3) return Action.Outcome3;
    else if (outcomeNumber === 4) return Action.Outcome4;
    else return null;
  }

  updateUI = (): void => {
    this.initializeScanItems();
    this.initializeImportItems();
    this.initializeCaptureItems();
    this.detectChanges();
  };

  detectChanges = (): void => {
    if (!this.cdr['destroyed']) {
      this.cdr.detectChanges();
    }
  };

  ngOnDestroy() {
    this.subscriptions.unsubscribe();
  }

  workflowsLoaded = (workflows: Array<Workflow>) => {
    if (!workflows.length) {
      const noProfilesFound = this.translate.instant('Scanning.NoProfilesFound');

      this.workflowDataSource.options.push({
        name: noProfilesFound,
        value: '',
      });

      this.toastr.info(noProfilesFound);

      this.isProfileAvailable = false;
      if (!this.cdr['destroyed']) this.cdr.detectChanges();
    } else {
      this.destinations = workflows;
      this.isProfileAvailable = true;
      this.destinations.forEach((c) => {
        this.workflowDataSource.options.push({
          name: c.name,
          value: c.id,
        });
      });

      const lastSelectedWorkflowId = this.userPreferencesService.getPreference(this.LastSelectedWorfklowId);

      if (lastSelectedWorkflowId) {
        this.selectedWorkflow = this.destinations.find((w) => w.id === lastSelectedWorkflowId) || this.destinations[0];
      } else {
        this.selectedWorkflow = this.destinations[0];
      }

      this.selectedWorkflowId = this.selectedWorkflow.id;

      this.createWorkflowsList();

      this.initializeScanItems();
      this.captureService.setDestination(this.selectedWorkflow);

      if (!this.cdr['destroyed']) {
        this.cdr.detectChanges();
      }
    }
  };

  initializeScanItems() {
    this.scanSplitItems = [];

    const scanToFolder = {
      action: Action.ScanToFolder,
      image: 'assets/icons/icnScanCapture.svg',
      enabled: this.isCaptureFolderOrDocumentSelected() && this.scanningToFolderAllowed() && this.oneItemSelected,
    };
    this.scanSplitItems.push(scanToFolder);

    const scanNew = {
      action: Action.ScanNew,
      image: '',
      enabled: this.isWorkflowSelected() && this.scanningAllowed(),
    };
    this.scanSplitItems.push(scanNew);

    const scanMorePages = {
      action: Action.ScanMorePages,
      image: '',
      enabled:
        this.isCaptureDocumentSelected() &&
        this.scanningMorePagesAllowed() &&
        this.documentLocked &&
        this.oneItemSelected,
    };
    this.scanSplitItems.push(scanMorePages);

    const reScanPage = {
      action: Action.RescanPage,
      image: '',
      enabled:
        this.isCapturePageSelected() && this.rescanningPagesAllowed() && this.documentLocked && this.oneItemSelected,
    };
    this.scanSplitItems.push(reScanPage);

    const reScanDocument = {
      action: Action.RescanDocument,
      image: '',
      enabled:
        this.isCaptureDocumentSelected() &&
        this.rescanningPagesAllowed() &&
        this.documentLocked &&
        this.oneItemSelected,
    };
    this.scanSplitItems.push(reScanDocument);
  }

  initializeImportItems() {
    this.importSplitItems = [];

    const importToFolder = {
      action: Action.ImportToFolder,
      image: 'assets/icons/icnImport.svg',
      enabled: this.isCaptureFolderOrDocumentSelected() && this.importingToFolderAllowed() && this.oneItemSelected,
    };
    this.importSplitItems.push(importToFolder);

    const importNew = {
      action: Action.ImportNew,
      image: '',
      enabled: this.isWorkflowSelected() && this.importingAllowed(),
    };
    this.importSplitItems.push(importNew);

    const importMorePages = {
      action: Action.ImportMorePages,
      image: '',
      enabled:
        this.isCaptureDocumentSelected() &&
        this.importingMorePagesAllowed() &&
        this.documentLocked &&
        this.oneItemSelected,
    };
    this.importSplitItems.push(importMorePages);

    const reimportPage = {
      action: Action.ReimportPage,
      image: '',
      enabled:
        this.isCapturePageSelected() && this.reimportingPagesAllowed() && this.documentLocked && this.oneItemSelected,
    };
    this.importSplitItems.push(reimportPage);

    const reimportDocument = {
      action: Action.ReimportDocument,
      image: '',
      enabled:
        this.isCaptureDocumentSelected() &&
        this.reimportingPagesAllowed() &&
        this.documentLocked &&
        this.oneItemSelected,
    };
    this.importSplitItems.push(reimportDocument);
  }

  initializeCaptureItems() {
    this.captureSplitItems = [];

    const captureToFolder = {
      action: Action.CaptureToFolder,
      image: 'assets/icons/camera-solid.svg',
      enabled: this.isCaptureFolderOrDocumentSelected() && this.importingToFolderAllowed() && this.oneItemSelected,
    };
    this.captureSplitItems.push(captureToFolder);

    const captureNew = {
      action: Action.CaptureNew,
      image: '',
      enabled: this.isWorkflowSelected() && this.importingAllowed(),
    };
    this.captureSplitItems.push(captureNew);

    const captureMorePages = {
      action: Action.CaptureMorePages,
      image: '',
      enabled:
        this.isCaptureDocumentSelected() &&
        this.importingMorePagesAllowed() &&
        this.documentLocked &&
        this.oneItemSelected,
    };
    this.captureSplitItems.push(captureMorePages);

    const recapturePage = {
      action: Action.RecapturePage,
      image: '',
      enabled:
        this.isCapturePageSelected() && this.reimportingPagesAllowed() && this.documentLocked && this.oneItemSelected,
    };

    this.captureSplitItems.push(recapturePage);

    const recaptureDocument = {
      action: Action.RecaptureDocument,
      image: '',
      enabled:
        this.isCaptureDocumentSelected() &&
        this.reimportingPagesAllowed() &&
        this.documentLocked &&
        this.oneItemSelected,
    };

    this.captureSplitItems.push(recaptureDocument);
  }

  loadWorkflowsFailed = (err: any) => {
    this.toastr.error(this.translate.instant('Common.LoadingDataError'));

    if (!this.cdr['destroyed']) {
      this.cdr.detectChanges();
    }
  };

  invokeAction = (event: ActionEvent): void => {
    this.invokedAction = event.action;

    switch (event.action) {
      case Action.ScanNew:
        this.onScan();
        break;
      case Action.ScanToFolder:
        this.onScanToFolder();
        break;
      case Action.ImportToUnselectedFolder:
        this.onImportToUnSelectedFolder(event as FolderActionEvent, false);
        break;
      case Action.ScanToUnselectedFolder:
        this.onScanToUnSelectedFolder(event as FolderActionEvent);
        break;
      case Action.ScanMorePages:
        this.onScanMorePages();
        break;
      case Action.RescanPage:
        this.onRescanPage();
        break;
      case Action.RescanDocument:
        this.onRescanDocument(event);
        break;
      case Action.ImportToFolder:
        this.onImportToFolder(false);
        break;
      case Action.ImportNew:
        this.onImport(false);
        break;
      case Action.ImportMorePages:
        this.onImportMorePages(false);
        break;
      case Action.ReimportPage:
        this.onReimportPage(false);
        break;
      case Action.ReimportDocument:
        this.onReimportDocument(event, false);
        break;
      case Action.CaptureNew:
        this.onImport(true);
        break;
      case Action.CaptureToFolder:
        this.onImportToFolder(true);
        break;
      case Action.CaptureMorePages:
        this.onImportMorePages(true);
        break;
      case Action.RecapturePage:
        this.onReimportPage(true);
        break;
      case Action.RecaptureDocument:
        this.onReimportDocument(event, true);
        break;
      case Action.Outcome1:
        this.executeOutcome(1);
        break;
      case Action.Outcome2:
        this.executeOutcome(2);
        break;
      case Action.Outcome3:
        this.executeOutcome(3);
        break;
      case Action.Outcome4:
        this.executeOutcome(4);
        break;
      case Action.GenerateRoutingSheet:
        this.executeGenerateRoutingSheet();
        break;
    }
  };

  executeGenerateRoutingSheet = (): void => {
    if (!this.selectedWorkflow) return;

    this.spinner.show(
      this.translate.instant('Scanning.generating-workflow-routingsheet'),
      document.getElementById('spinnerPanel')
    );

    this.hydraApi.workflowGenerateRoutingSheet(
      this.selectedWorkflow.id,
      this.onGeneratedRoutingSheet,
      this.onFailedGenerateRoutingSheet
    );
  };

  onGeneratedRoutingSheet = (blob): void => {
    this.spinner.hide();
    saveAs(blob, `${this.selectedWorkflow.name}-routingsheet.pdf`);
  };

  onFailedGenerateRoutingSheet = (): void => {
    this.spinner.hide();
    this.toastr.error(this.translate.instant('Scanning.failed-generate-workflow-routingsheet'));
  };

  executeOutcome(outcomeNumber: number) {
    if (!this.currentDocument && !this.currentFolderHasItems()) return;

    const outcomesCount = this.outcomeButtons.length;
    const outcomeIndex = outcomesCount - outcomeNumber;

    if (outcomeIndex >= 0 && outcomeIndex < outcomesCount) {
      const element = $(':focus');
      element.blur();
      this.onClick(this.outcomeButtons[outcomeIndex]);
    }
  }

  importer: ImporterCallback;

  import = (i: ImporterCallback, multiple: boolean, useCamera: boolean): void => {
    this.importer = i;

    if (!useCamera) {
      this.openFilePicker(multiple);
    } else {
      this.openCaptureModal();
    }
  };

  openFilePicker(multiple: boolean) {
    const fileInput = document.querySelector('#file') as HTMLInputElement;
    fileInput.multiple = multiple;
    fileInput.click();
  }

  openCaptureModal() {
    this.captureModal = this.ngxSmartModalService.getModal('CaptureToICModal');
    this.captureModal.open();
    this.showScanner = true;
  }

  onImport = (useCamera: boolean): void => {
    if (!this.anyImportingAllowed()) return;

    this.import(
      (files: File[]) => {
        this.captureService.addBatchWithMultipleDocuments(files);
      },
      true,
      useCamera
    );
  };

  closeCaptureModal() {
    this.showScanner = false;
    this.captureModal?.close();
  }

  onImportToFolder = (useCamera: boolean): void => {
    if (!this.isCaptureFolderOrDocumentSelected() || !this.importingToFolderAllowed()) return;

    const currentFolderId = this.currentDocument ? this.currentDocument.parentFolderId : this.currentFolder.id;
    const folder = this.captureService.findFolder(currentFolderId);
    if (!folder) return;

    if (folder.parentWorkflowFolder == null) {
      this.onImport(useCamera);
    } else {
      this.import(
        (files: File[]) => {
          for (let i = 0; i < files.length; i++) {
            this.captureService.addFiletoFolder(
              files[i],
              Filename.getFileNameWithoutExtension(files[i].name),
              Filename.getFileExtension(files[i].name),
              folder
            );
          }
        },
        true,
        useCamera
      );
    }
  };

  onImportToUnSelectedFolder(event: FolderActionEvent, useCamera: boolean) {
    this.import(
      (files: File[]) => {
        for (let i = 0; i < files.length; i++) {
          if (event.folder.parentWorkflowFolder == null) {
            this.captureService.addBatchWithMultipleDocuments(files);
          } else {
            this.captureService.addFiletoFolder(
              files[i],
              Filename.getFileNameWithoutExtension(files[i].name),
              Filename.getFileExtension(files[i].name),
              event.folder
            );
          }
        }
      },
      true,
      useCamera
    );
  }

  onScanToUnSelectedFolder(event: FolderActionEvent) {
    if (event.folder.parentWorkflowFolder == null) {
      this.scan.emit(
        new ScanToWorkflow(
          this.captureService.currentWorkflow.id,
          this.captureService.currentWorkflow.scanSettings,
          new ScanOptions(this.translate.currentLang, true),
          new DocumentProperties()
        )
      );
    } else {
      this.scan.emit(
        new ScanToFolder(
          this.captureService.currentWorkflow.id,
          event.folder.id,
          this.captureService.currentWorkflow.scanSettings
        )
      );
    }
  }

  onImportMorePages = (useCamera: boolean): void => {
    if (!this.isCaptureDocumentSelected() || !this.importingMorePagesAllowed()) return;

    const folder = this.captureService.findFolder(this.currentDocument.parentFolderId);
    const result = folder.findDocument(this.currentDocument.id);

    this.import(
      (files: File[]) => {
        this.captureService.appendPagesToDocument(
          result.workItem,
          result.document,
          files,
          Filename.getFileExtension(files[0].name)
        );
      },
      false,
      useCamera
    );
  };

  fileListToArray(fileList: FileList) {
    const filesArray = [];

    for (let i = 0; i < fileList.length; i++) {
      filesArray.push(fileList[i]);
    }

    return filesArray;
  }

  onReimportPage = (useCamera: boolean): void => {
    if (!this.isCapturePageSelected() || !this.reimportingPagesAllowed()) return;

    this.import(
      (files: File[]) => {
        this.captureService.reImportPageToDocument(files[0]);
      },
      false,
      useCamera
    );
  };

  onReimportDocument(event: ActionEvent, useCamera: boolean) {
    if (event.action === Action.RecaptureDocument) event.action = Action.ReimportDocument;

    const { workItem, doc } = this.getDocumentAndWorkItemToInvokeAction(event);
    this.import(
      (files: File[]) => {
        this.captureService.replaceDocument(workItem.id, doc.id, files[0], Filename.getFileExtension(files[0].name));
      },
      false,
      useCamera
    );
  }

  onFilePickerChange() {
    const fileInput = document.querySelector('#file') as HTMLInputElement;
    const files = Array.from(fileInput.files);

    this.onImportFiles(files);
  }

  onImportFiles(files: File[]) {
    if (!this.importer) return;
    if (!files.length) return;

    this.inProgress.open('app-scan.title-import');
    this.importer(files);
    this.importer = null;
  }

  onFinishCapturing(captures: ICapture[]) {
    this.closeCaptureModal();
    this.onImportFiles([
      createPDFFromImages(
        captures.map((c) => c.capturedImage),
        'document.pdf',
        false
      ),
    ]);
  }

  onScan = (): void => {
    this.scan.emit(
      new ScanToWorkflow(
        this.captureService.currentWorkflow.id,
        this.captureService.currentWorkflow.scanSettings,
        new ScanOptions(this.translate.currentLang, true),
        new DocumentProperties()
      )
    );
  };

  onScanToFolder = (): void => {
    if (!this.isCaptureFolderOrDocumentSelected() || !this.scanningToFolderAllowed()) {
      this.captureService.showNoScanFolderError();
      return;
    }

    const currentFolderId = this.currentDocument ? this.currentDocument.parentFolderId : this.currentFolder.id;
    const folder = this.captureService.findFolder(currentFolderId);

    if (folder && folder.parentWorkflowFolder == null) {
      this.onScan();
    } else {
      this.scan.emit(
        new ScanToFolder(
          this.captureService.currentWorkflow.id,
          currentFolderId,
          this.captureService.currentWorkflow.scanSettings
        )
      );
    }
  };

  onScanMorePages = (): void => {
    if (!this.isCaptureDocumentSelected() || !this.scanningMorePagesAllowed()) {
      this.captureService.showNoScanDocumentError();
      return;
    }

    this.scan.emit(
      new ScanAdditionalPages(
        this.captureService.currentWorkflow.id,
        this.currentDocument.id,
        this.currentDocument.parentFolderId,
        this.captureService.currentWorkflow.scanSettings,
        new ScanOptions(this.translate.currentLang, false),
        new DocumentProperties()
      )
    );
  };

  onRescanPage = (): void => {
    if (!this.isCapturePageSelected() || !this.rescanningPagesAllowed()) {
      this.captureService.showNoScanPageError();
      return;
    }

    this.scan.emit(
      new RescanPage(
        this.captureService.currentWorkflow.id,
        this.currentDocument.id,
        this.currentDocument.parentFolderId,
        this.currentPage.order,
        this.captureService.currentWorkflow.scanSettings,
        new ScanOptions(this.translate.currentLang, false),
        new DocumentProperties()
      )
    );
  };

  onRescanDocument(event: ActionEvent) {
    const { workItem, doc } = this.getDocumentAndWorkItemToInvokeAction(event);
    this.scan.emit(
      new RescanDocument(
        workItem.id,
        doc.id,
        this.captureService.currentWorkflow.scanSettings,
        new ScanOptions(this.translate.currentLang, false),
        new DocumentProperties()
      )
    );
  }

  getDocumentAndWorkItemToInvokeAction(event: ActionEvent): { doc: IDocument | CapturedDocument; workItem: WorkItem } {
    let workItem: WorkItem;
    let doc: IDocument | CapturedDocument;

    if (event.documentResult) {
      workItem = event.documentResult.workItem;
      doc = event.documentResult.document;
    } else {
      workItem = this.captureService.getCurrentWorkItem();
      doc = this.captureService.getCurrentDocument();
    }

    return { doc, workItem };
  }

  onWorkflowChanged(): void {
    if (!this.destinations.length) {
      return;
    }

    this.selectedWorkflow = this.destinations.find((w) => w.id === this.selectedWorkflowId);
    this.captureService.setDestination(this.selectedWorkflow);

    this.userPreferencesService.setPreference(this.LastSelectedWorfklowId, this.selectedWorkflowId);
  }

  checkIfItemIsNotInViewAndScroll(index: number) {
    const selectedElement = $(`.${this.classNamePrefix + index}`);
    const container = $('.projects-wrapper');
    if (
      selectedElement[0] &&
      container[0] &&
      !this.calculations.isVisibleInScrollableElement(selectedElement, container, false)
    ) {
      this.directiveRef.scrollToElement(`.${this.classNamePrefix + index}`, 0, 100);
    }
  }

  emptyInputValue(event) {
    event.target.value = null;
  }

  onClick = (outcome: IOutcome): void => {
    this.captureService.executeOutcome(outcome);
  };

  discard() {
    this.discardDocuments.emit();
  }

  anyScanningAllowed = (): boolean => {
    if (
      this.scanningAllowed() ||
      this.rescanningPagesAllowed() ||
      this.scanningMorePagesAllowed() ||
      this.scanningToFolderAllowed()
    )
      return true;

    return false;
  };

  anyImportingAllowed = (): boolean => {
    if (
      this.importingAllowed() ||
      this.reimportingPagesAllowed() ||
      this.importingMorePagesAllowed() ||
      this.importingToFolderAllowed()
    )
      return true;

    return false;
  };

  isWorkflowSelected = (): boolean => this.selectedWorkflow != null;

  isCaptureDocumentSelected = (): boolean => this.currentDocument != null;

  currentStepHasScanCapability = (capability: string): boolean => {
    if (!this.userState.hasRolePermission(RolePermissions.CaptureScan)) return false;

    const ws = this.getCurrentWorkflowStep();
    if (ws == null) return false;

    return ws.hasCapability(capability);
  };

  currentStepHasImportCapability = (capability: string): boolean => {
    if (!this.userState.hasRolePermission(RolePermissions.CaptureImport)) return false;

    const ws = this.getCurrentWorkflowStep();
    if (ws == null) return false;

    return ws.hasCapability(capability);
  };

  getCurrentWorkflowStep = (): WorkflowStep => {
    if (this.currentDocument != null) return this.captureService.getWorkflowStepById(this.currentDocument.parentStepId);
    if (this.currentFolder != null) return this.captureService.getWorkflowStepById(this.currentFolder.workflowStepId);

    return null;
  };

  scanningAllowed = (): boolean => this.currentStepHasScanCapability(Capabilities.allowCaptureScan);

  scanningToFolderAllowed = (): boolean => this.currentStepHasScanCapability(Capabilities.allowCaptureScanToFolder);

  rescanningPagesAllowed = (): boolean => this.currentStepHasScanCapability(Capabilities.allowCaptureRescanPage);

  scanningMorePagesAllowed = (): boolean => this.currentStepHasScanCapability(Capabilities.allowCaptureScanMorePages);

  importingAllowed = (): boolean => this.currentStepHasScanCapability(Capabilities.allowCaptureImport);

  importingToFolderAllowed = (): boolean => this.currentStepHasScanCapability(Capabilities.allowCaptureImportToFolder);

  reimportingPagesAllowed = (): boolean => this.currentStepHasScanCapability(Capabilities.allowCaptureReimportPage);

  importingMorePagesAllowed = (): boolean =>
    this.currentStepHasScanCapability(Capabilities.allowCaptureImportMorePages);

  canShowQRCode = (): boolean => {
    return (
      this.scanningAllowed() &&
      this.importingAllowed() &&
      this.userState.userProfile.mobileBrand == Branding.IntelligentCapture
    );
  };

  isCaptureFolderOrDocumentSelected = (): boolean => {
    return this.currentDocument != null || this.currentFolder != null;
  };

  currentFolderHasItems = (): boolean => {
    if (!this.currentFolder) return false;
    return this.currentFolder.folderHasItems();
  };

  isCapturePageSelected = (): boolean => {
    if (!this.currentPage || !this.currentDocument) return false;

    return true;
  };

  getCurrentWorkflowIndex(): number {
    return this.destinations.findIndex((dist) => dist.id === this.selectedWorkflow.id);
  }

  generateQRCode = (): void => {
    this.spinner.show(this.translate.instant('qr-code.generating-qr-code'), document.body);
    this.hydraApi.getDocumentCaptureQRCode(this.selectedWorkflow.id, this.qrcodeLoaded, this.qrcodeFailed);
  };

  qrcodeLoaded = (file: Blob) => {
    file = file.slice(0, file.size, 'image/bmp');

    var urlCreator = window.URL || window.webkitURL;
    this.ngxSmartModal.setModalData(
      { title: 'qr-code.qr-code-title', imageUrl: urlCreator.createObjectURL(file) },
      'qrCodeModal',
      true
    );
    this.ngxSmartModal.open('qrCodeModal');
    this.spinner.hide();
  };

  qrcodeFailed = (err: any) => {
    const that = this;
    const reader = new FileReader();
    reader.onload = function () {
      that.spinner.hide();
      const errorMessage = reader.result ? reader.result : that.translate.instant('qr-code.qr-code-failed');
      that.errorService.showErrorsModal([errorMessage], 'qr-code.generate-qr-code');
    };

    reader.readAsText(err.error);
    Log.error(that.translate.instant('qr-code.qr-code-failed'), err);
  };

  createWorkflowsList = (): void => {
    this.fieldWorkflow = new FieldDefinition({
      type: MetaDataType.List,
      label: '',
      placeholder: '',
      required: false,
      datasource: this.workflowDataSource,
    });
  };
}
