import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ActiveToast, ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';

import { SystemMetaData } from '../../../blocks/core/systemMetaData';
import { WorkItem } from '../../../blocks/models/work-item';
import { DocumentCaptureService } from '../capture-services';
import { CaptureUpdateService } from '../capture-services/capture-update.service';

@Component({
  selector: 'app-delivery-error',
  templateUrl: './delivery-error.component.html',
  styleUrls: ['./delivery-error.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class DeliveryErrorComponent implements OnInit, OnDestroy {
  toastMessagesArr: Array<ActiveToast<any>> = [];

  constructor(
    private capture: DocumentCaptureService,
    private captureUpdate: CaptureUpdateService,
    private toastr: ToastrService,
    private translate: TranslateService
  ) {
    this.subscriptions = new Subscription();
    this.subscriptions.add(
      this.captureUpdate.onChangeDocument.subscribe(() => {
        this.workItem = this.capture.getCurrentWorkItem();
      })
    );
  }

  workItem: WorkItem;
  subscriptions: Subscription;

  ngOnInit() {
    this.workItem = this.capture.getCurrentWorkItem();
  }

  ngOnDestroy() {
    this.subscriptions.unsubscribe();

    this.toastMessagesArr.forEach((m) => this.toastr.clear(m.toastId));
  }

  get deliveryError() {
    if (!this.workItem) return null;

    const deliveryErrorMetaData = this.workItem.getMetaData(SystemMetaData.DeliveryErrorMessage);
    return deliveryErrorMetaData && deliveryErrorMetaData.value ? deliveryErrorMetaData : null;
  }

  get deliveryErrorTitle() {
    return this.translate.instant('Document_Capture.delivery-error');
  }

  showErrorDetails() {
    const errorMessage = this.toastr.error(this.deliveryError.value as string, this.deliveryErrorTitle, {
      closeButton: true,
      tapToDismiss: false,
      disableTimeOut: true,
    });

    this.toastMessagesArr.push(errorMessage);
  }
}
