import {
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
  Renderer2,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';

import { TranslateService } from '@ngx-translate/core';
import { BsDropdownDirective } from 'ngx-bootstrap/dropdown';
import { Subscription } from 'rxjs';

import { ToastrService } from 'ngx-toastr';
import {
  ContextMenuComponent,
  DelayService,
  FieldDataSource,
  FieldDefinition,
  FolderTreeAction,
  IMetaDataDefinition,
  MetaDataCascadingOptionsDataSource,
  MetaDataDefinition,
  MetaDataType,
} from 'shared-code-lib';
import { Action, ActionEvent, FolderActionEvent } from '../../../../blocks/core/actions';
import { Filename } from '../../../../blocks/core/fileName';
import { Log } from '../../../../blocks/core/log';
import { FolderItem } from '../../../../blocks/models/folder-item';
import { IOutcome, Outcome } from '../../../../blocks/models/outcome';
import { WorkItem } from '../../../../blocks/models/work-item';
import { Workflow } from '../../../../blocks/models/workflow';
import { WorkflowFolder } from '../../../../blocks/models/workflow-folder';
import { InteractiveWorkflowStep, WorkflowStep, WorkflowStepSortType } from '../../../../blocks/models/workflow-step';
import { ActionService } from '../../../../blocks/services/action.service';
import { DragDropService } from '../../../../blocks/services/drag-drop.service';
import { HydraApiService } from '../../../../blocks/services/hydra-api.service';
import { ModalsUtilitiesService } from '../../../../blocks/services/modals-utilities.service';
import { SpinnerStateService } from '../../../../blocks/services/spinner-state.service';
import { UserStateService } from '../../../../blocks/services/user-state.service';
import { Capabilities, DocumentListStyle, DragData } from '../../capture-models';
import { CaptureConstants } from '../../capture-models/capture-constants';
import { DocumentsGridSortingConfig, SortingType } from '../../capture-models/sorting';
import { DocumentCaptureService } from '../../capture-services';
import { CaptureUpdateService } from '../../capture-services/capture-update.service';
import { DocumentListStyleService } from '../../capture-services/document-list-style.service';
import { FolderViewEventsService } from '../../capture-services/folder-view-events.service';
import { OutcomesService } from '../../capture-services/outcomes.service';
import { SelectedItemsService } from '../../capture-services/selected-items.service';

declare const $: any;

@Component({
  selector: 'app-folder',
  templateUrl: './folder.component.html',
  styleUrls: ['./folder.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class FolderComponent implements OnInit, OnDestroy {
  @ViewChild('folderEl', { static: true }) folderEl: ElementRef;
  @ViewChild('wrapper') wrapper: ElementRef;
  @ViewChild('menu') menu: ElementRef;
  @Output() openParentFolder = new EventEmitter();
  @Input() workflow: Workflow;
  @Input() folder: WorkflowFolder;
  @Input() isBatch: boolean;
  @Output() toggleSpinner = new EventEmitter<boolean>();
  @Output() documentLoaded = new EventEmitter();
  @Input() stepMetadataDefinitions: Array<MetaDataDefinition> = [];
  @Input() stepMetadataDefinitionsFields: Array<FieldDefinition> = [];
  @Input() contextMenu: ContextMenuComponent;

  childrenWorkItems: Array<WorkItem>;
  isExpanded: boolean;
  isWorkflowRoot: boolean;
  loadingItems: boolean;
  subscriptions: Subscription;
  dropEnabled: boolean;
  discardOutcome: IOutcome;
  canDeleteDocuments: boolean;

  canImportNew: boolean;
  canScanNew: boolean;
  canDragDocuments: boolean;

  hoverOver: boolean;
  hoverOverMenu: boolean;
  showMenu: boolean;
  dropup: boolean;
  isGridView: boolean;
  isFolderSelected: boolean;
  multipleItemsSelected: boolean;

  sortingConfig = new DocumentsGridSortingConfig();

  folderString = CaptureConstants.folder;

  field = 'documents[0].filename';
  dir: 'asc' | 'desc' = 'asc';
  outcomes: IOutcome[];

  constructor(
    private hydraApi: HydraApiService,
    private folderView: FolderViewEventsService,
    private capture: DocumentCaptureService,
    private captureUpdate: CaptureUpdateService,
    private translate: TranslateService,
    private spinnerState: SpinnerStateService,
    private actionService: ActionService,
    private delay: DelayService,
    private userStateService: UserStateService,
    private documentListStyleService: DocumentListStyleService,
    private toastr: ToastrService,
    private selectedItemsService: SelectedItemsService,
    private dragDropService: DragDropService,
    private outcomesService: OutcomesService,
    private renderer: Renderer2,
    private modalsUtilitiesService: ModalsUtilitiesService
  ) {
    this.isExpanded = false;
    this.isWorkflowRoot = false;
    this.childrenWorkItems = [];
    this.subscriptions = new Subscription();
    this.dropEnabled = false;
  }

  changeSort(field, dir) {
    this.field = field;
    this.dir = dir;
  }

  ngOnInit() {
    Log.enter('FolderComponent.ngOnInit');

    // set default sorting
    this.defaultSort();

    this.renderer.listen(this.folderEl.nativeElement, 'contextmenu', (event) => {
      this.contextMenu.open(event, null, null, this.getFolderContextMenuActions());
    });

    try {
      this.isWorkflowRoot = this.folder.workflowStepId === this.folder.parentID;
      this.isGridView = this.documentListStyleService.getStyle() === DocumentListStyle.Grid;
      this.capture.addToFolders(this.folder);

      if (this.isWorkflowRoot && (this.folder.folders.length || this.folder.itemCount > 0)) {
        this.isExpanded = true;
        this.folder.expanded = true;
        this.loadFolderItems();
      }

      const ws: WorkflowStep = this.capture.getWorkflowStepById(this.folder.workflowStepId);
      if (ws) {
        if (ws.hasCapability(Capabilities.deleteDocument)) {
          this.discardOutcome = {
            id: Outcome.SendToDoneId,
            outcomeType: '',
            text: this.translate.instant('Scanning.Discard'),
            label: this.translate.instant('Scanning.Discard'),
            enabled: true,
            requiresConfirmation: true,
            requiresValidMetaData: false,
            nextId: '',
          };

          this.canDeleteDocuments = true;
        }

        this.canImportNew = ws.hasCapability(Capabilities.allowCaptureImport);
        this.canScanNew = ws.hasCapability(Capabilities.allowCaptureScan);
        this.canDragDocuments = ws.hasCapability(Capabilities.allowDropDocument);
        if (this.isWorkflowRoot) {
          this.loadStepMetadataDefinitions(ws);
        }
      }

      this.subscriptions.add(this.captureUpdate.onExpandFolder.subscribe((f) => this.expandFolder(f, true)));
      this.subscriptions.add(this.captureUpdate.onCollapseFolder.subscribe((f) => this.expandFolder(f, false)));
      this.subscriptions.add(this.actionService.onActivateAction.subscribe(this.invokeAction));

      this.subscriptions.add(
        this.folderView.onAddFolder.subscribe((folderView) => {
          if (folderView.parentId === this.folder.id) {
            const newFolder = new WorkflowFolder(
              folderView.id,
              folderView.parentId,
              this.folder,
              folderView.workflowStepId,
              folderView.name,
              folderView.itemCount,
              [],
              null
            );

            this.folder.folders.push(newFolder);
          }
        })
      );

      this.subscriptions.add(
        this.folderView.onDeleteFolder.subscribe((folderView) => {
          if (folderView.parentId === this.folder.id) {
            this.folder.folders = this.folder.folders.filter((folder) => {
              return folder.id !== folderView.id;
            });
          }
        })
      );

      this.subscriptions.add(
        this.folderView.onUpdateFolder.subscribe((folderView) => {
          if (folderView.id === this.folder.id) {
            this.folder.itemCount = folderView.itemCount;
          }
        })
      );

      this.subscriptions.add(
        this.folderView.onAddItem.subscribe((folderItem) => {
          if (this.folder.id === folderItem.parentId) {
            if (this.capture.hasChangedByCurrentUser(folderItem.workItem, false)) {
              this.openParentFolder.emit();
            }

            this.addFolderItem(folderItem);
          }
        })
      );

      this.subscriptions.add(this.folderView.onUpdateItem.subscribe((folderItem) => this.updateFolderItem(folderItem)));

      this.subscriptions.add(
        this.folderView.onDeleteItem.subscribe((folderItem) => {
          if (this.folder.id === folderItem.parentId && this.folder.items) {
            this.capture.unselectDocument(folderItem.documents[0].id);

            this.folder.items = this.folder.items.filter((f) => {
              return f.workItem.id !== folderItem.workItem.id;
            });
          }
        })
      );

      this.documentListStyleService.onChangeDocumentListStyle.subscribe((style: DocumentListStyle) => {
        this.isGridView = style === DocumentListStyle.Grid;
      });

      this.subscriptions.add(
        this.folderView.onSortGridColumnData.subscribe((sortingConfig: DocumentsGridSortingConfig) => {
          if (sortingConfig.workflowStepId !== this.folder.workflowStepId) return;

          this.sortingConfig = sortingConfig;
          this.sortDocuments(this.sortingConfig);
        })
      );

      this.onChangedSelectedItems();
      this.subscriptions.add(this.selectedItemsService.onChangeSelectedItems.subscribe(this.onChangedSelectedItems));
    } catch (e) {
      Log.error('FolderComponent.ngOnInit', e);
    }

    this.subscriptions.add(
      this.captureUpdate.onChangeOutcomeButtons.subscribe((outcomes) => {
        this.outcomes = outcomes;
      })
    );
  }

  private defaultSort() {
    const workflowStep = this.workflow.workflowSteps.find((a) => a.id == this.folder.workflowStepId);

    if (workflowStep) {
      const interactivestep = new InteractiveWorkflowStep(workflowStep);
      this.dir = interactivestep.sortDirection == 0 ? 'asc' : 'desc';

      if (interactivestep.sortBy == WorkflowStepSortType.DateCreated) {
        this.field = 'documents[0].fileTimeCreated';
      } else {
        this.field = 'documents[0].filename';
      }
    }
  }

  ngOnDestroy() {
    this.subscriptions.unsubscribe();
    this.capture.removeFolder(this.folder.id);
  }

  getFolderContextMenuActions(): Array<FolderTreeAction> {
    this.selectFolder();

    const actions: Array<FolderTreeAction> = [];
    if (this.canDeleteDocuments) {
      let actionName: string;
      if (this.isFolderSelected && this.multipleItemsSelected) {
        actionName = this.translate.instant('document-actions.delete');
      } else {
        actionName = this.translate.instant('folder.DeleteDocuments');
      }
      actions.push({
        name: actionName,
        action: () => {
          this.deleteSelectedItems();
        },
        disabled: !this.folder.folderHasItems(),
      });
    }

    if (this.canImportNew && !this.isWorkflowRoot && !(this.isFolderSelected && this.multipleItemsSelected)) {
      actions.push({
        name: this.translate.instant('folder.ImportNew'),
        action: () => {
          this.addDocument();
        },
      });
    }

    if (this.canScanNew && !this.isWorkflowRoot && !(this.isFolderSelected && this.multipleItemsSelected)) {
      actions.push({
        name: this.translate.instant('folder.ScanNew'),
        action: () => {
          this.scanDocument();
        },
      });
    }

    // sort actions

    if (!(this.isFolderSelected && this.multipleItemsSelected)) {
      actions.push(
        {
          name: this.translate.instant('folder.SortByFileNameAsc'),
          action: () => {
            this.changeSort('documents[0].filename', 'asc');
          },
          disabled: !this.isFolderSelected || !this.folder.folderHasItems(),
        },
        {
          name: this.translate.instant('folder.SortByFileNameDesc'),
          action: () => {
            this.changeSort('documents[0].filename', 'desc');
          },
          disabled: !this.isFolderSelected || !this.folder.folderHasItems(),
        },
        {
          name: this.translate.instant('folder.SortByDateAsc'),
          action: () => {
            this.changeSort('documents[0].fileTimeCreated', 'asc');
          },
          disabled: !this.isFolderSelected || !this.folder.folderHasItems(),
        },
        {
          name: this.translate.instant('folder.SortByDateDesc'),
          action: () => {
            this.changeSort('documents[0].fileTimeCreated', 'desc');
          },
          disabled: !this.isFolderSelected || !this.folder.folderHasItems(),
        }
      );
    }

    this.outcomes.forEach((outcome: IOutcome) => {
      if (outcome.enabled) {
        actions.push({
          name: outcome.label,
          action: () => {
            this.capture.executeOutcome(outcome);
          },
          disabled: !this.isFolderSelected || !this.folder.folderHasItems(),
        });
      }
    });

    return actions;
  }

  getColumns = (): IMetaDataDefinition[] => {
    const columns: IMetaDataDefinition[] = [];

    this.stepMetadataDefinitions.forEach((x) => {
      if (x.type == MetaDataType.CascadingOptions) {
        ((x as MetaDataDefinition).dataSource as MetaDataCascadingOptionsDataSource).dataSet.forEach((o) => {
          const optionMDD = this.userStateService.findMetaDataById(o.metaDataId);

          columns.push(optionMDD);
        });

        return;
      }

      columns.push(x);
    });

    return columns;
  };

  getFolderItem = (id: string): FolderItem => {
    const item: FolderItem = this.folder.items.find((i, index) => {
      return i.workItem.id === id;
    });
    return item;
  };

  onChangedSelectedItems = () => {
    this.isFolderSelected = this.selectedItemsService.isItemSelected(this.folder.id, this.folder.workflowStepId);
    this.multipleItemsSelected = this.selectedItemsService.areMultipleItemsSelected();
  };

  invokeAction = (event: ActionEvent): void => {
    if (!this.isFolderSelected) return;

    if (event.action === Action.DeleteSelectedItem || event.action === Action.CtrlDeleteSelectedItem) {
      if (!this.canDeleteDocuments || this.modalsUtilitiesService.isAnyModalOpened()) return;

      this.deleteSelectedItems();
      return;
    }
  };

  updateFolderItem = (folderItem: FolderItem): void => {
    if (this.folder.id === folderItem.parentId) {
      if (this.folder.items !== null && this.folder.items.length) {
        const item: FolderItem = this.getFolderItem(folderItem.workItem.id);
        if (item) {
          for (let i = 0; i < this.folder.items.length; i++) {
            if (this.folder.items[i].workItem.id === folderItem.workItem.id) {
              this.folder.items[i] = folderItem;
              this.folderView.sortGridColumnData(this.sortingConfig);
              this.expandAndSelectDocument(folderItem);
              break;
            }
          }
        } else {
          this.addFolderItem(folderItem);
        }
      } else {
        this.folder.items = [];
        this.addFolderItem(folderItem);
      }
    } else {
      if (this.folder.items) {
        this.folder.items = this.folder.items.filter((f) => {
          return f.workItem.id !== folderItem.workItem.id;
        });

        if (!this.existsInSameWorkflowStep(folderItem)) {
          const currentWorkItem = this.capture.getCurrentWorkItem();
          if (currentWorkItem && currentWorkItem.id == folderItem.workItem.id) {
            this.capture.unselectDocument(folderItem.documents[0].id);
          }
        }
      }
    }
  };

  existsInSameWorkflowStep = (folderItem: FolderItem): boolean => {
    return this.capture.getCurrentStepId() == folderItem.workItem.workflowStepId;
  };

  addFolderItem(folderItem: FolderItem) {
    if (!this.folder || !this.folder.items) return;

    this.folder.items.push(folderItem);
    this.folderView.sortGridColumnData(this.sortingConfig);
    this.delay.execute(() => {
      this.captureUpdate.scrollToDocument(folderItem.documents[0].id);
    }, 500);
    this.expandAndSelectDocument(folderItem);
  }

  expandAndSelectDocument(folderItem: FolderItem) {
    if (this.capture.hasChangedByCurrentUser(folderItem.workItem, true)) {
      if (!this.isExpanded) {
        this.toggleExpand();
      }

      this.capture.selectDocument(folderItem.documents[0], folderItem.workItem, this.folder.id);

      this.openParentFolder.emit();
    }
  }

  expandFolder = (folder: WorkflowFolder, expand: boolean): void => {
    if (folder.id != this.folder.id) return;

    if (this.isExpanded == expand) return;

    this.toggleExpand();
  };

  toggleExpand() {
    this.isExpanded = !this.isExpanded;
    this.folder.expanded = this.isExpanded;

    if (
      (this.isExpanded && !this.folder.items) ||
      (this.isExpanded && this.folder.itemCount !== this.folder.items.length)
    ) {
      this.loadFolderItems();
    }
  }

  loadFolderItems() {
    this.folder.items = [];

    this.loadingItems = true;
    this.hydraApi.getWorkflowFolderItems(
      this.folder.id,
      this.workflow.id,
      (items: Array<FolderItem>) => {
        this.folder.items = items;
        this.onDocumentLoaded();
        this.loadingItems = false;

        this.folderView.sortGridColumnData(this.sortingConfig);
      },
      (error) => {
        this.toastr.error(this.translate.instant('errors.failed-get-workflow-folder-items'));

        this.loadingItems = false;
      }
    );
  }

  onAddNewFolder() {
    if (!this.isExpanded) {
      this.toggleExpand();
    }

    this.openParentFolder.emit();
  }

  selectFolder(event: MouseEvent = null) {
    if (event) {
      const target = event.target as HTMLElement;
      if (target && target.className === 'icomoon-more') return;
    }

    this.capture.selectFolder(this.folder, event?.ctrlKey || false);
    this.expandFolder(this.folder, true);
  }

  onDragOver(dragData: DragData) {
    const ws = this.capture.getWorkflowStepById(this.folder.workflowStepId);
    if (ws == null) return;

    this.dropEnabled = false;

    if (!dragData) {
      this.dropEnabled = ws.hasCapability(Capabilities.allowDropExternalDocument) && this.isPrimaryStep();
      return;
    }

    const _document = dragData.element as WorkItem;
    if (_document.workflowStepId === this.folder.workflowStepId && dragData.elementParentId !== this.folder.id) {
      if (!ws.hasCapability(Capabilities.allowDropDocument)) return;

      this.dropEnabled = true;
      if (!this.isExpanded) {
        this.toggleExpand();
      }
    } else if (
      dragData.elementType === CaptureConstants.page &&
      this.folder.workflowStepId === dragData.elementParentStepId
    ) {
      if (!ws.hasCapability(Capabilities.allowDropDocumentPage)) return;

      this.dropEnabled = true;
      if (!this.isExpanded) {
        this.toggleExpand();
      }
    }
  }

  onDragLeave(dragData: DragData) {
    this.dropEnabled = false;
  }

  onDrop(dragData: DragData) {
    const ws = this.capture.getWorkflowStepById(this.folder.workflowStepId);
    if (ws == null) return;

    if (dragData instanceof DragEvent) {
      if (!ws.hasCapability(Capabilities.allowDropExternalDocument) || !this.isPrimaryStep()) return;
      this.dropFiles(dragData);
    }

    const firstDragData = this.dragDropService.dragDataArray[0];

    this.dragDropService.dragDataArray.forEach((dragDataItem) => {
      dragDataItem.target = firstDragData.target;
      dragDataItem.targetType = firstDragData.targetType;

      if (dragDataItem.elementType === CaptureConstants.document) {
        if (!ws.hasCapability(Capabilities.allowDropDocument)) return;
        this.dropDocument(dragDataItem);
      }

      if (
        dragDataItem.elementType === CaptureConstants.page &&
        this.folder.workflowStepId === dragDataItem.elementParentStepId
      ) {
        if (!ws.hasCapability(Capabilities.allowDropDocumentPage)) return;
        this.dropPage(dragDataItem);
      }
    });

    this.dragDropService.clearDragData();
    this.dropEnabled = false;
  }

  isPrimaryStep() {
    const currentStep = this.getCurrentStep();
    const primaryInteractiveStep = this.workflow.workflowSteps.find((step) => step instanceof InteractiveWorkflowStep);
    return currentStep.id === primaryInteractiveStep.id;
  }

  dropFiles(dragData: DragEvent) {
    //TODO: if (!(this.getCurrentStep() instanceof CaptureWorkflowStep)) return;

    const files = dragData.dataTransfer.files;
    if (!files.length) return;
    for (let i = 0; i < files.length; i++) {
      const fileName = Filename.getFileNameWithoutExtension(files[i].name);
      let fileExtension = Filename.getFileExtension(files[i].name);
      this.capture.addFiletoFolder(files[i], fileName, fileExtension, this.folder);
    }
  }

  dropDocument(dragData: DragData) {
    this.capture.handleDragDropDocuments(
      dragData,
      this.folder.workflowStepId,
      this.capture.getGroupingFolderValues(this.folder)
    );
  }

  dropPage(dragData: DragData) {
    this.onToggleSpinner(true);
    this.capture
      .addPageToFolder(dragData.element, dragData.elementPosition, this.folder)
      .then(() => {
        this.onToggleSpinner(false);
      })
      .catch(() => {
        this.onToggleSpinner(false);
      });
  }

  getCurrentStep() {
    return this.capture.getWorkflowStepById(this.folder.workflowStepId);
  }

  getDocumentCountText(): string {
    if (!this.folder || this.folder.itemCount <= 0) {
      return '';
    }

    const pageSuffix = this.translate.instant(
      this.folder.itemCount === 1 ? 'Document_Capture.document' : 'Document_Capture.documents'
    );

    return `(${this.folder.itemCount} ${pageSuffix})`;
  }

  onToggleSpinner(state: boolean) {
    if (state) {
      this.translate
        .get('Document_Capture.UpdatingDocument')
        .subscribe((value) => this.spinnerState.show(value, document.getElementById('app-spinner-panel')));
    } else {
      this.spinnerState.hide();
    }
  }

  onMouseEnter(isMenu?: boolean) {
    if (isMenu) {
      this.hoverOverMenu = true;
    } else {
      this.hoverOver = true;
    }
  }

  onMouseLeave(isMenu?: boolean) {
    this.delay.execute(() => {
      if (isMenu) {
        this.hoverOverMenu = false;
      } else {
        this.hoverOver = false;
      }
    }, 10);
  }

  onClickMenuButton(event: Event, dropdown: BsDropdownDirective) {
    console.log('onClickMenuButton');

    this.selectFolder();

    this.contextMenu.open(event, null, null, this.getFolderContextMenuActions());
  }

  addDocument() {
    this.hide();
    const actionEvent = new FolderActionEvent(Action.ImportToUnselectedFolder, this.folder);
    this.actionService.activateAction(actionEvent);
  }

  scanDocument() {
    this.hide();
    const actionEvent = new FolderActionEvent(Action.ScanToUnselectedFolder, this.folder);
    this.actionService.activateAction(actionEvent);
  }

  hide() {
    this.hoverOver = false;
    this.hoverOverMenu = false;
  }

  onHidden() {
    this.showMenu = false;
  }

  loadStepMetadataDefinitions = (workflowStep: WorkflowStep): void => {
    const interactiveWorkflowStep = workflowStep as InteractiveWorkflowStep;

    for (const id of interactiveWorkflowStep.metaDataIds) {
      const metadataDefinition = this.userStateService.findMetaDataById(id);
      if (metadataDefinition == null) {
        continue;
      }

      if (metadataDefinition.type === MetaDataType.LineItems) {
        continue;
      }

      this.stepMetadataDefinitions.push(metadataDefinition as MetaDataDefinition);
    }

    this.initializeStepMetaDataDefinitionsFields();
  };

  initializeStepMetaDataDefinitionsFields() {
    this.stepMetadataDefinitions.forEach((mdd) => {
      const fieldDefinition = mdd.asFieldDefinition();
      fieldDefinition.label = '';

      fieldDefinition.datasource = FieldDataSource.create(mdd, this.hydraApi);

      this.stepMetadataDefinitionsFields.push(fieldDefinition);
    });
  }

  trackWorkItemId(index, item) {
    return item.id;
  }

  trackFolderId(index, folder) {
    return folder.id;
  }

  onDocumentLoaded() {
    this.documentLoaded.emit();
  }

  onGridColumnClicked(sortingIndex: number) {
    let sortingType = SortingType.Asc;
    if (this.sortingConfig.index === sortingIndex) {
      sortingType = this.sortingConfig.type === SortingType.Asc ? SortingType.Desc : SortingType.Asc;
    }

    this.sortingConfig.index = sortingIndex;
    this.sortingConfig.type = sortingType;
    this.sortingConfig.workflowStepId = this.folder.workflowStepId;

    this.folderView.sortGridColumnData(this.sortingConfig);
  }

  sortDocuments = (sortingConfig: DocumentsGridSortingConfig) => {
    if (
      this.sortingConfig.index === undefined ||
      this.sortingConfig.type === SortingType.None ||
      this.sortingConfig.workflowStepId !== this.folder.workflowStepId ||
      !this.folder.items
    ) {
      return;
    }

    this.folder.items.sort((first, second) => {
      let firstValue: string, secondValue: string;

      if (sortingConfig.index === -1) {
        firstValue = first.documents[0].filename;
        secondValue = second.documents[0].filename;
      } else if (sortingConfig.index >= 0) {
        firstValue = this.getMetaDataValue(first.workItem, sortingConfig.index);
        secondValue = this.getMetaDataValue(second.workItem, sortingConfig.index);
      }

      return this.getCompareResult(sortingConfig.index, firstValue, secondValue) * Number(sortingConfig.type);
    });
  };

  getMetaDataValue = (workItem: WorkItem, metaDataIndex: number): string => {
    return workItem.getMetaData(this.getColumns()[metaDataIndex].name).value.toString();
  };

  getCompareResult = (metaDataIndex: number, firstValue: string, secondValue: string): number => {
    const metaDataType = metaDataIndex === -1 ? MetaDataType.Text : this.getColumns()[metaDataIndex].type;

    if (metaDataType === MetaDataType.Integer || metaDataType === MetaDataType.Decimal) {
      return Number(firstValue) - Number(secondValue);
    }

    if (metaDataType === MetaDataType.Date) {
      return Date.parse(firstValue) <= Date.parse(secondValue) ? -1 : 1;
    }

    return firstValue.localeCompare(secondValue);
  };

  showSortingIcon(index: number): boolean {
    return (
      this.sortingConfig.workflowStepId === this.folder.workflowStepId &&
      this.sortingConfig.type !== SortingType.None &&
      this.sortingConfig.index === index
    );
  }

  getSortingIconClass(): string {
    if (this.sortingConfig.type === SortingType.Asc) return 'k-i-sort-asc-sm';
    if (this.sortingConfig.type === SortingType.Desc) return 'k-i-sort-desc-sm';
    return '';
  }

  deleteSelectedItems(): void {
    this.hide();
    this.outcomesService.confirmDeleteSelectedItems(this.confirmedDeleteSelectedItems);
  }

  confirmedDeleteSelectedItems = (data: any) => {
    this.capture.transitionSelectedItems(data.id);
  };
}
