import {
  Component,
  OnInit,
  Input,
  HostListener,
  ElementRef,
  ChangeDetectorRef,
  ViewEncapsulation,
  OnChanges,
  SimpleChanges,
} from '@angular/core';
import { ISplitButtonItem } from './split-item';
import { ActionService } from '../../services/action.service';
import { ActionEvent } from '../../core/actions';
import { TranslateService } from '@ngx-translate/core';
import { KeyboardActionMapping } from '../../core/keyboardActionMapping';

@Component({
  selector: 'app-splitbutton',
  templateUrl: './split-button.component.html',
  styleUrls: ['./split-button.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class SplitButtonComponent implements OnInit, OnChanges {
  @Input() items: ISplitButtonItem[];
  showDropDown = false;
  mainItem: ISplitButtonItem;

  constructor(
    private elementRef: ElementRef,
    private cdr: ChangeDetectorRef,
    private actionService: ActionService,
    private translate: TranslateService
  ) {}

  ngOnInit() {
    this.setMainItem();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.items) this.setMainItem();
  }

  setMainItem = (): void => {
    this.mainItem = this.items && this.items.length ? this.items[0] : null;
  };

  activateAction = (item: ISplitButtonItem): void => {
    this.actionService.activateAction(new ActionEvent(item.action));
  };

  getItemTitle = (item: ISplitButtonItem): string => {
    const action = this.translate.instant(`Actions.${item.action}`);
    const mapping = KeyboardActionMapping.getTooltipFor(item.action,this.translate);

    return `${action} ${mapping}`;
  };

  getItemActionName = (item: ISplitButtonItem): string => {
    return this.translate.instant(`Actions.${item.action}`);
  };

  getItemKeyMapping = (item: ISplitButtonItem): string => {
    return KeyboardActionMapping.getTooltipFor(item.action,this.translate);
  };

  getItems = (): ISplitButtonItem[] => {
    if (!this.items || !this.items.length) {
      return [];
    }

    return this.items.filter(i => i != this.items[0]);
  };

  onSplitButtonClicked() {
    if (this.mainItem && this.mainItem.enabled) this.activateAction(this.mainItem);
  }

  @HostListener('document:click', ['$event', '$event.target'])
  onClick(event: MouseEvent, targetElement: HTMLElement): void {
    if (!targetElement) return;

    if (this.elementRef.nativeElement.querySelector('.split-dropdown').contains(targetElement)) {
      this.showDropDown = !this.showDropDown;
    } else {
      this.showDropDown = false;
    }

    this.cdr.detectChanges();
  }
}
