import { Component, ElementRef, OnDestroy, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { NgxSmartModalComponent, NgxSmartModalService } from 'ngx-smart-modal';
import { Subject, Subscription } from 'rxjs';

import { ToastrService } from 'ngx-toastr';
import { MetaDataCollection, UserPreferencesService } from 'shared-code-lib';
import { Log } from '../../blocks/core/log';
import { FolderItem } from '../../blocks/models/folder-item';
import { LockStatus } from '../../blocks/models/lock-status';
import { UpdateFieldData, UpdateGroupOfFields } from '../../blocks/models/update-field-data';
import { HydraApiService } from '../../blocks/services/hydra-api.service';
import { SpinnerStateService } from '../../blocks/services/spinner-state.service';
import { UserStateService } from '../../blocks/services/user-state.service';
import { CapturedDocument, DocumentListStyle, IndexingPanels, Page, Preference } from './capture-models';
import { ScanPages, ScanPagesResult } from './capture-models/scan-commands';
import { DocumentCaptureService, IndexingPanelService } from './capture-services';
import { CaptureUpdateService } from './capture-services/capture-update.service';
import { DocumentListStyleService } from './capture-services/document-list-style.service';
import { DocumentLockingService } from './capture-services/document-locking.service';
import { FolderViewEventsService } from './capture-services/folder-view-events.service';
import { ScanAppService } from './capture-services/scan-app.service';

@Component({
  templateUrl: './document-capture.component.html',
  styleUrls: ['./document-capture.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class DocumentCaptureComponent implements OnInit, OnDestroy {
  @ViewChild('contentWrapper', { static: true }) contentWrapper: ElementRef;
  selectedPage: Page;
  showLineItemPanel: boolean;
  widthResizedSubject: Subject<void>;
  heightResizedSubject: Subject<void>;
  focusedPanel = IndexingPanels[0];
  routingWrapper: HTMLElement;
  confirmationModal: NgxSmartModalComponent;
  showDriverModal: boolean;
  scanModal: NgxSmartModalComponent;
  captureModal: NgxSmartModalComponent;
  readonly panelsWrapperKey = 'doc-cap-pWrapperWidth';
  readonly panelsDocumentGridWrapperKey = 'doc-cap-pDocumentGridWrapperWidth';
  readonly indexingPanelKey = 'doc-cap-indexingPHeight';
  readonly lineItemsPanelKey = 'doc-cap-lineItemPHeight';
  currentStepHasIndexing = false;
  scanPages: ScanPages;
  isGridView: boolean;

  subscriptions = new Subscription();

  selectedDocument: CapturedDocument;
  documentLocked: boolean;

  readonly DocumentListStylePreference = 'DocumentListStylePreferenceId';

  constructor(
    public captureService: DocumentCaptureService,
    private indexingService: IndexingPanelService,
    private captureUpdate: CaptureUpdateService,
    private scanAppService: ScanAppService,
    private userStateService: UserStateService,
    public ngxSmartModalService: NgxSmartModalService,
    private spinnerState: SpinnerStateService,
    private translate: TranslateService,
    private documentListStyleService: DocumentListStyleService,
    private documentLockingService: DocumentLockingService,
    private userPreferencesService: UserPreferencesService,
    private hydraApi: HydraApiService,
    private folderView: FolderViewEventsService,
    private toastr: ToastrService
  ) {
    this.widthResizedSubject = new Subject<void>();
    this.heightResizedSubject = new Subject<void>();
  }

  ngOnInit() {
    Log.enter('DocumentCaptureComponent.ngOnInit');

    try {
      this.subscriptions.add(this.documentLockingService.lockStatusChanged.subscribe(this.lockStatusChanged));

      this.subscriptions.add(
        this.captureUpdate.onChangeDocument.subscribe((doc) => {
          this.documentLocked = false;
          this.selectedDocument = doc;
          this.hydraApi.onDocumentChanged();
        })
      );

      this.subscriptions.add(this.folderView.onUpdateItem.subscribe(this.onWorkItemUpdated));

      this.subscriptions.add(
        this.scanAppService.onDocumentScanned.subscribe((scanresult: ScanPagesResult) =>
          scanresult.scanpages.process(this.captureService, scanresult.data)
        )
      );

      this.subscriptions.add(
        this.scanAppService.onTransferDocument.subscribe(() => {
          this.closeScanModal();
        })
      );

      this.subscriptions.add(
        this.indexingService.onChangeLineItemIndexing.subscribe((show: boolean) => {
          this.showLineItemPanel = show;
          this.currentStepHasIndexing = this.captureService.currentStepHasIndexing();
        })
      );

      this.subscriptions.add(
        this.documentListStyleService.onChangeDocumentListStyle.subscribe((style: DocumentListStyle) => {
          this.isGridView = style === DocumentListStyle.Grid;

          if (style === DocumentListStyle.Tree) {
            setTimeout(() => {
              this.captureService.reInitIndexing();
              this.reloadCurrentWorkitemMetadata();
            }, 100);
          }
        })
      );

      let documentListStyle = this.userPreferencesService.getPreference(this.DocumentListStylePreference);
      if (!documentListStyle) {
        documentListStyle = DocumentListStyle.Tree;
      }

      this.documentListStyleService.update(documentListStyle);
      this.isGridView = documentListStyle === DocumentListStyle.Grid;
    } catch (e) {
      Log.error('DocumentCaptureComponent.ngOnInit', e);
    }
  }

  ngOnDestroy() {
    this.subscriptions.unsubscribe();
    this.captureService.reset();

    this.documentLockingService.unlockDocuments();
  }

  onWorkItemUpdated = (folderItem: FolderItem): void => {
    if (!this.selectedDocument || folderItem.documents[0].id != this.selectedDocument?.id) return;

    const doc = this.selectedDocument.document;
    if (folderItem.documents.find((x) => x.pageCount == doc.pageCount && x.size == doc.size)) return;

    this.captureService.selectDocument(folderItem.documents[0], folderItem.workItem, folderItem.parentId, null, true);
  };

  lockStatusChanged = (ls: LockStatus): void => {
    if (!this.selectedDocument || !this.selectedDocument.workitem) return;

    if (ls.id == this.selectedDocument.workitem.id) {
      this.documentLocked = ls.locked && ls.lockedByUserId == this.userStateService.user.id && !ls.expired();
    }
  };

  widthResized() {
    this.widthResizedSubject.next();
  }

  onResize(preference: Preference) {
    this.userPreferencesService.setPreference(preference.name, preference.value);
  }

  getPreferenceValue(key: string, defaultValue: string): string {
    return this.userPreferencesService.getPreference(key) || defaultValue;
  }

  getDocumentPanelWidthPreference() {
    return this.getPreferenceValue(this.getPanelWrapperKey(), '25');
  }

  getPanelWrapperKey() {
    return this.isGridView ? this.panelsDocumentGridWrapperKey : this.panelsWrapperKey;
  }

  heightResized() {
    this.heightResizedSubject.next();
  }

  addFiles(files: File[]) {
    this.captureService.addBatchWithMultipleDocuments(files);
  }

  discardAllDocs() {
    this.confirmationModal.close();
    this.captureService.reset();
  }

  fillCurrentField(updateFieldData: UpdateFieldData) {
    this.indexingService.fillCurrentField(updateFieldData);
  }

  fillGroupOfFields(updateGroupOfFields: UpdateGroupOfFields) {
    this.indexingService.fillGroupOfFieldsValues(updateGroupOfFields);
  }

  scan(startscan: ScanPages) {
    this.scanPages = startscan;
    this.showDriverModal = true;
    this.scanModal = this.ngxSmartModalService.getModal('ScanToICModal');
    this.scanModal.open();
    this.scanModal.onClose.subscribe(() => {
      this.showDriverModal = false;
    });
  }

  openConfirmationModal() {
    this.confirmationModal = this.ngxSmartModalService.getModal('confirmationModal');
    this.confirmationModal.open();
  }

  closeScanModal() {
    this.scanModal?.close();
  }

  onToggleSpinner(state: boolean) {
    if (state) {
      this.translate
        .get('Document_Capture.UpdatingDocument')
        .subscribe((value) => this.spinnerState.show(value, this.contentWrapper.nativeElement));
    } else {
      this.spinnerState.hide();
    }
  }

  reloadCurrentWorkitemMetadata = (): void => {
    if (!this.selectedDocument?.workItemId) return;

    this.hydraApi.getWorkItemMetaData(
      this.selectedDocument.workItemId,
      this.currentWorkitemMetadataLoaded,
      this.currentWorkitemMetadataLoadFailed
    );
  };

  currentWorkitemMetadataLoaded = (mdc: MetaDataCollection) => (this.selectedDocument.workitem.metadata = mdc);

  currentWorkitemMetadataLoadFailed = (err: any) => {
    Log.error('DocumentCaptureComponent.currentWorkitemMetadataLoadFailed', err);
    this.toastr.error(this.translate.instant('Document_Capture.metadata-load-failed'));
  };
}
