import {
  AfterViewChecked,
  AfterViewInit,
  ChangeDetectorRef,
  Component,
  effect,
  EventEmitter,
  OnDestroy,
  OnInit,
  Output,
  viewChild,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';

import { TranslateService } from '@ngx-translate/core';
import { NgxSmartModalService } from 'ngx-smart-modal';
import { ToastrService } from 'ngx-toastr';
import { fromEvent, Subject, Subscription } from 'rxjs';

import { CdkVirtualScrollViewport } from '@angular/cdk/scrolling';
import { PerfectScrollbarComponent } from 'ngx-perfect-scrollbar-portable';
import {
  BlurEvent,
  BulkValidationRequest,
  DelayService,
  DropDownService,
  FieldDataSource,
  FieldDefinition,
  FieldDisplayMode,
  ILocation,
  IMetaData,
  IMetaDataDefinition,
  LineItemConfiguration,
  MetaDataCascadingOptionsDataSource,
  MetaDataCollection,
  MetaDataCollectionDictionary,
  MetaDataDefinition,
  MetaDataLineItems,
  MetaDataType,
  MetaDataValidity,
  SaveLookupsEvent,
} from 'shared-code-lib';
import { Log } from '../../../blocks/core/log';
import { LockStatus } from '../../../blocks/models/lock-status';
import { WorkItem } from '../../../blocks/models/work-item';
import { HydraApiService } from '../../../blocks/services/hydra-api.service';
import { SpinnerStateService } from '../../../blocks/services/spinner-state.service';
import { UserStateService } from '../../../blocks/services/user-state.service';
import {
  CapturedDocument,
  GroupOfValuesObj,
  IndexingPanels,
  UpdateLineItemsEvent,
  ValueLocation,
  ValueObj,
} from '../capture-models';
import { DocumentCaptureService, IndexingPanelService } from '../capture-services';
import { CaptureUpdateService } from '../capture-services/capture-update.service';
import { DocumentLockingService } from '../capture-services/document-locking.service';
import { SelectedItemsService } from '../capture-services/selected-items.service';

@Component({
  selector: 'app-line-item-indexing',
  templateUrl: './line-item-indexing.component.html',
  styleUrls: ['./line-item-indexing.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class LineItemIndexingComponent implements OnInit, AfterViewInit, AfterViewChecked, OnDestroy {
  @ViewChild('virtualScrollViewport', { static: false }) virtualScrollViewport: CdkVirtualScrollViewport;
  cdkScrollElement = viewChild<CdkVirtualScrollViewport | undefined>('virtualScrollViewport');

  @ViewChild('tableWrapper', { static: false }) tableWrapper: PerfectScrollbarComponent;
  @Output() setCurrentCell = new EventEmitter<string>();
  @Output() removeFocus = new EventEmitter();
  lineItemPanel = IndexingPanels['LineItemIndexing'];
  isInitialized: boolean;
  selectedRowIndex: number;
  selectedCellIndex: number;
  subscriptions: Subscription;
  toBeDeletedIndex: number;
  value = '';
  emptyLinesShouldBeRemoved: boolean;
  defaultRowValues: string;

  lineItemMetaDataId: string;
  lineItemMetaDataName: string;
  lineItemMDD: Array<IMetaDataDefinition>;
  lineItems: MetaDataLineItems;
  headerLabel: string;
  loadingDefaultValues = false;

  selectedDocument: CapturedDocument;
  documentLocked: boolean;
  oneDocumentSelected: boolean;
  allowedTypesForTotals: MetaDataType[] = [MetaDataType.Integer, MetaDataType.Decimal];
  isBusy: boolean;

  virtualScrollViewportStyles = { width: '0px', height: '0px' };

  addNewLineItemSubject = new Subject();

  constructor(
    public capture: DocumentCaptureService,
    private captureUpdate: CaptureUpdateService,
    private indexingService: IndexingPanelService,
    private cdr: ChangeDetectorRef,
    private delay: DelayService,
    private hydraApi: HydraApiService,
    private ngxSmartModalService: NgxSmartModalService,
    private translate: TranslateService,
    private toastr: ToastrService,
    private dropDown: DropDownService,
    private spinner: SpinnerStateService,
    private documentLockingService: DocumentLockingService,
    public userStateService: UserStateService,
    private selectedItemsService: SelectedItemsService
  ) {
    this.subscriptions = new Subscription();

    effect(() => {
      const nativeScrollElement = this.cdkScrollElement()?.elementRef.nativeElement;
      if (!nativeScrollElement) return;

      const last = nativeScrollElement.lastElementChild;
      if (nativeScrollElement.firstElementChild && last) {
        nativeScrollElement.replaceChild(nativeScrollElement.firstElementChild, nativeScrollElement.lastElementChild);
        nativeScrollElement.insertBefore(last, nativeScrollElement.firstElementChild);
      }
    });
  }

  ngOnInit() {
    this.subscriptions.add(this.documentLockingService.lockStatusChanged.subscribe(this.lockStatusChanged));

    this.subscriptions.add(
      this.captureUpdate.onChangeDocument.subscribe((doc) => {
        this.documentLocked = false;
        this.selectedDocument = doc;
        this.emptyLinesShouldBeRemoved = true;
      })
    );

    this.subscriptions.add(
      this.captureUpdate.onUpdateLineItemTableHead.subscribe((header) => {
        this.isInitialized = true;
        this.lineItemMetaDataId = header.id;
        this.lineItemMetaDataName = header.name;
        this.lineItemMDD = header.columns;
        this.headerLabel = header.label;
        if (!this.cdr['destroyed']) {
          this.cdr.detectChanges();
        }
      })
    );

    this.subscriptions.add(
      this.captureUpdate.onUpdateLineItemTableBody.subscribe((event: UpdateLineItemsEvent) => {
        if (this.loadingDefaultValues) {
          console.log('waiting for loading default values');

          this.addNewLineItemSubject
            .subscribe(() => {
              this.updateLineItems(event);
            })
            .unsubscribe();
        } else {
          this.updateLineItems(event);
        }
      })
    );

    this.subscriptions.add(
      this.indexingService.onTakeFocus.subscribe((panel: number) => {
        if (panel !== this.lineItemPanel) {
          this.delay.execute(this.reset, 10);
        }
      })
    );

    this.subscriptions.add(
      this.indexingService.onChangeCurrentFieldValue.subscribe((valueObj: ValueObj) => {
        if (valueObj.panel === this.lineItemPanel) {
          const mdd = new MetaDataDefinition(this.lineItemMDD[this.selectedCellIndex]);

          this.value = valueObj.updateFieldData.value;
          this.setFieldValue(this.selectedRowIndex, mdd, this.value, true);
        }
      })
    );

    this.subscriptions.add(
      this.indexingService.onFillGroupOfValues.subscribe((groupOfFieldsValuesObj: GroupOfValuesObj) => {
        if (this.lineItemMDD[this.selectedCellIndex].type === MetaDataType.LargeText) {
          this.setFieldValue(
            this.selectedRowIndex,
            this.lineItemMDD[this.selectedCellIndex],
            groupOfFieldsValuesObj.UpdateGroupOfFields.values.join('\n'),
            true
          );
          return;
        }

        this.mapGroupOfValues(groupOfFieldsValuesObj.UpdateGroupOfFields.values);
      })
    );

    this.onChangedSelectedItems();
    this.subscriptions.add(this.selectedItemsService.onChangeSelectedItems.subscribe(this.onChangedSelectedItems));
  }

  ngAfterViewInit(): void {
    this.addStopWritingInFieldsWhileLoadingListener();
  }

  ngAfterViewChecked(): void {
    this.setVirtualScrollViewportStyles();
  }

  addStopWritingInFieldsWhileLoadingListener = () => {
    const spinnerElement = this.getLineItemIndexingElement();
    if (!spinnerElement) return;

    this.subscriptions.add(fromEvent(spinnerElement, 'keydown').subscribe(this.stopWritingInFieldsWhileLoading));
  };

  stopWritingInFieldsWhileLoading = (event: Event) => {
    if (!this.isBusy) return;

    event.preventDefault();
    event.stopPropagation();
    return false;
  };

  onChangedSelectedItems = () => {
    this.oneDocumentSelected = this.selectedItemsService.isOneDocumentSelected();
  };

  private updateLineItems(event: UpdateLineItemsEvent) {
    const valuesWithValidLocation: Array<ValueLocation> = [];
    if (!this.lineItemChanged(event.body)) return;
    this.lineItems = event.body;
    if (this.emptyLinesShouldBeRemoved) this.removeEmptyLines();
    this.updateLineItemsReference();
    this.lineItems.forEach((row, rowIndex) => {
      row.metadata.forEach((item) => {
        this.lineItemMDD.forEach((mdd, itemIndex) => {
          if (item.location && item.name === mdd.name.toLowerCase()) {
            const { location, validity } = item;
            const fieldId = this.getFieldId(mdd.id, rowIndex, itemIndex);
            valuesWithValidLocation.push({ fieldId: fieldId, location: location, validity: validity });
          }
        });
      });
    });
    this.captureUpdate.hasValuesWithValidLocation(valuesWithValidLocation);
  }

  lineItemChanged = (updatedLineItem: MetaDataLineItems): boolean => {
    if (!updatedLineItem && !this.lineItems) return false;
    if ((!updatedLineItem && this.lineItems) || (updatedLineItem && !this.lineItems)) return true;

    const currentItems = [...this.lineItems];
    for (let i = 0; i < updatedLineItem.length; i++) {
      const index = currentItems.findIndex((x) => this.getRowKey(x) == this.getRowKey(updatedLineItem[i]));
      if (index == -1) return true;

      currentItems.splice(index, 1);
    }

    for (let i = 0; i < currentItems.length; i++) {
      if (this.getRowKey(currentItems[i]) != this.defaultRowValues) return true;
    }

    return false;
  };

  getRowKey = (row: MetaDataCollection): string => {
    return JSON.stringify(row.metadata.map((x) => `${x.name}_${x.value}_${x.validity}`).sort());
  };

  ngOnDestroy() {
    this.subscriptions.unsubscribe();
  }

  removeEmptyLines = (): void => {
    this.lineItems = this.lineItems.filter(
      (x) => x.metadata.length > 0 && x.metadata.filter((y) => y.value != '').length > 0
    ) as MetaDataLineItems;
    this.emptyLinesShouldBeRemoved = false;
  };

  lockStatusChanged = (ls: LockStatus): void => {
    if (!this.selectedDocument || !this.selectedDocument.workitem) return;

    if (ls.id == this.selectedDocument.workitem.id) {
      this.documentLocked = ls.locked && ls.lockedByUserId == this.userStateService.user.id && !ls.expired();
    }
  };

  reset = () => {
    this.value = '';
    this.selectedRowIndex = null;
    this.selectedCellIndex = null;
  };

  onAddClicked(): void {
    this.addItem(true);
  }

  addItem(saveValues = false): Promise<void> {
    this.loadingDefaultValues = true;
    this.showSpinner('Indexing.loading-line-items-defaults');

    return this.getLineItemDefaults()
      .then((mdDic) => {
        const mdc = new MetaDataCollection(mdDic);
        this.addMetaDataCollectionRow(mdc, saveValues);
        this.defaultRowValues = this.getRowKey(mdc);

        this.hideSpinner();
        this.cdr.detectChanges();
        this.loadingDefaultValues = false;
        this.addNewLineItemSubject.next(true);
      })
      .catch((err) => {
        this.toastr.error(this.translate.instant('Indexing.line-items-defaults-load-error'));

        const mdc = new MetaDataCollection();
        for (let i = 0; i < this.lineItemMDD.length; i++) {
          mdc.set(this.lineItemMDD[i].name, '', MetaDataValidity.unknown);
        }

        this.addMetaDataCollectionRow(mdc);

        this.hideSpinner();
        this.cdr.detectChanges();
        this.loadingDefaultValues = false;
        this.addNewLineItemSubject.next(true);
      });
  }

  showSpinner(msg: string) {
    this.isBusy = true;
    const spinnerElement = this.getLineItemIndexingElement();

    this.spinner.show(this.translate.instant(msg), spinnerElement);
  }

  getLineItemIndexingElement = (): HTMLElement => {
    return document.getElementById('line-item-indexing');
  };

  hideSpinner = (): void => {
    this.isBusy = false;
    this.spinner.hide();
  };

  getLineItemDefaults(): Promise<MetaDataCollectionDictionary> {
    return new Promise((resolve, reject) => {
      this.hydraApi.getLineItemDefaults(
        this.capture.getCurrentWorkItem().id,
        this.lineItemMetaDataId,
        (metadataDic: MetaDataCollectionDictionary) => {
          resolve(metadataDic);
        },
        (err) => {
          reject(err);
        }
      );
    });
  }

  getColumns = (): IMetaDataDefinition[] => {
    const columns: IMetaDataDefinition[] = [];
    this.lineItemMDD.forEach((x) => {
      if (x.type == MetaDataType.CascadingOptions) {
        ((x as MetaDataDefinition).dataSource as MetaDataCascadingOptionsDataSource).dataSet.forEach((o) => {
          const optionMDD = this.userStateService.findMetaDataById(o.metaDataId);
          columns.push(optionMDD);
        });
        return;
      }

      columns.push(x);
    });

    return columns;
  };

  getColSpan = (mdd: IMetaDataDefinition): number => {
    if (mdd.type == MetaDataType.CascadingOptions)
      return ((mdd as MetaDataDefinition).dataSource as MetaDataCascadingOptionsDataSource).dataSet.length;

    return 1;
  };

  addMetaDataCollectionRow(mdc: MetaDataCollection, saveValues: boolean = false): void {
    mdc.placeholder = true;
    this.lineItems.push(mdc);
    this.updateLineItemsReference();
    this.cdr.detectChanges();
    this.virtualScrollViewport.scrollToIndex(this.lineItems.length - 1);
    this.selectCell(0, this.lineItems.length - 1);

    const selectedMdd = this.lineItemMDD[this.selectedCellIndex];

    if (saveValues) {
      this.setFieldValue(this.lineItems.length - 1, selectedMdd, mdc.metadata[this.selectedCellIndex].value, false);
    }

    this.emitCurrentCellChange(selectedMdd.name);
  }

  selectCell(cellIndex: number, rowIndex: number, mdd?: IMetaDataDefinition, item?: MetaDataCollection) {
    this.value = '';

    let tempFieldId: string;

    if (mdd && item) {
      const location = this.getValueLocation(item, mdd);
      if (location && location.valid) {
        tempFieldId = this.getFieldId(mdd.id, rowIndex, cellIndex);
      }
    }

    this.captureUpdate.zoomIntoValue(tempFieldId);
    this.selectedRowIndex = rowIndex;
    this.selectedCellIndex = cellIndex;
    this.emitCurrentCellChange(this.lineItemMDD[cellIndex].name);
  }

  moveToNextCell(): Promise<void> {
    this.value = '';

    if (this.selectedCellIndex === this.lineItemMDD.length - 1) {
      if (this.selectedRowIndex === this.lineItems.length - 1) {
        return this.addItem();
      } else {
        this.selectedRowIndex += 1;
        this.selectedCellIndex = 0;
      }
    } else {
      this.selectedCellIndex += 1;
    }

    return Promise.resolve();
  }

  moveToPreviousCell() {
    this.value = '';

    if (this.selectedCellIndex === 0) {
      if (this.selectedRowIndex === 0) {
        this.selectedCellIndex = this.lineItemMDD.length - 1;
        this.selectedRowIndex = this.lineItems.length - 1;
      } else {
        this.selectedRowIndex -= 1;
        this.selectedCellIndex = this.lineItemMDD.length - 1;
      }
    } else {
      this.selectedCellIndex -= 1;
    }
  }

  emitCurrentCellChange(label: string) {
    this.indexingService.changeCurrentField(this.lineItemPanel, label);
  }

  confirmDelete(itemIndex: number): void {
    this.toBeDeletedIndex = itemIndex;

    this.ngxSmartModalService.setModalData(
      {
        title: 'MainHeader.ActionConfirmation',
        text: this.translate.instant('Document_Capture.Confirm_Delete_Line_Item'),
        buttonText: this.translate.instant('Common.Delete'),
        confirm: this.confirmedDelete,
        cancel: this.cancelDelete,
      },
      'actionConfirmModal',
      true
    );

    this.ngxSmartModalService.getModal('actionConfirmModal').open();
  }

  confirmedDelete = () => {
    this.ngxSmartModalService.getModal('actionConfirmModal').close();
    this.deleteItem(this.toBeDeletedIndex);
    this.toBeDeletedIndex = null;
  };

  cancelDelete = () => {
    this.ngxSmartModalService.getModal('actionConfirmModal').close();
    this.toBeDeletedIndex = null;
  };

  private deleteItem(itemIndex: number) {
    this.lineItems.splice(itemIndex, 1);
    this.updateLineItemsReference();

    this.showSpinner('Indexing.saving-items');
    this.capture
      .saveLineItems(this.lineItems)
      .then(this.hideSpinner, this.hideSpinner)
      .then(() => {
        if (this.lineItems.length) {
          if (this.selectedRowIndex === itemIndex) {
            if (itemIndex >= this.lineItems.length) {
              this.selectedRowIndex -= 1;
            }

            this.selectedCellIndex = 0;
          } else if (this.selectedRowIndex > itemIndex) {
            this.selectedRowIndex -= 1;
          }

          this.cdr.detectChanges();
          this.virtualScrollViewport.scrollToIndex(itemIndex);
        }

        if (!this.lineItems.length) {
          this.indexingService.takeFocus(IndexingPanels['AdvancedIndexing']);
        }
      });
  }

  onBlur(value: any, item: MetaDataCollection, mdd: IMetaDataDefinition, itemIndex: number) {
    event?.preventDefault();
    const shouldAddItem = (event as any)?.relatedTarget?.id == 'addLineItemButton';

    if (value instanceof BlurEvent) {
      mdd = value.mdd ?? mdd;
      value = value.value;
    }

    this.checkValues(value, item, mdd, itemIndex, false, shouldAddItem);
  }

  onTab(value: any, item: MetaDataCollection, mdd: IMetaDataDefinition, itemIndex: number) {
    event?.preventDefault();

    let checkValues: Promise<any>;

    if (value instanceof BlurEvent) {
      checkValues = this.checkValues(value.value, item, value.mdd ? value.mdd : mdd, itemIndex);
    } else {
      checkValues = this.checkValues(value, item, mdd, itemIndex);
    }

    if (checkValues) {
      checkValues.then(() => {
        return this.moveToNextCell();
      });
    } else {
      this.moveToNextCell()
        .then(() => {})
        .catch(() => {});
    }
  }

  onShiftTab(value: string, item: MetaDataCollection, mdd: IMetaDataDefinition, itemIndex: number) {
    event?.preventDefault();

    this.checkValues(value, item, mdd, itemIndex);
    this.moveToPreviousCell();
  }

  onReturn(value: string, item: MetaDataCollection, mdd: IMetaDataDefinition, rowIndex: number, cellIndex: number) {
    item.set(mdd.name, value, MetaDataValidity.unknown);

    this.checkValues(value, item, mdd, rowIndex, true).then(() => {
      this.captureUpdate.changeValueValidity({ id: mdd.id + rowIndex + cellIndex, validity: MetaDataValidity.valid });
    });
  }

  checkValues(
    value: string,
    item: MetaDataCollection,
    mdd: IMetaDataDefinition,
    itemIndex: number,
    force: boolean = false,
    addNewLineItem = false
  ) {
    const prevValue = this.getFieldValue(item, mdd);
    const isSuspiciousField = this.getValidity(item, mdd) === MetaDataValidity.suspicious;

    if (!this.isEqual(value, prevValue as string) || force || isSuspiciousField) {
      return this.setFieldValue(itemIndex, mdd, value, false, 0, addNewLineItem);
    }
  }

  isEqual(value1: string, value2: string): boolean {
    if (typeof value1 != 'string' || typeof value2 != 'string') return false;
    return value1.replace(/\n/g, '') === value2.replace(/\n/g, '');
  }

  getFieldDefinition(mdd: IMetaDataDefinition): FieldDefinition {
    const fd = (mdd as MetaDataDefinition).asFieldDefinition();
    fd.datasource = FieldDataSource.create(mdd as MetaDataDefinition, this.hydraApi);
    fd.label = '';
    fd.displayMode = FieldDisplayMode.grid;

    return fd;
  }

  getFieldValue(item: MetaDataCollection, mdd: IMetaDataDefinition): string {
    if (!item) return '';

    const md = item.get(mdd.name);
    if (!md) return '';

    return `${md.value}`;
  }

  getValueLocation(item: MetaDataCollection, mdd: IMetaDataDefinition): ILocation {
    if (!item) {
      return null;
    }

    const md = item.get(mdd.name);
    if (!md) {
      return null;
    }

    return md.location;
  }

  setFieldValue(
    itemIndex: number,
    mdd: IMetaDataDefinition,
    value: any,
    moveNext: boolean,
    counter: number = 0,
    addNewLineItem = false
  ) {
    // copy the current line items to be saved when validation returns
    // this way it preserves the instantly created row
    const currentWorkItem: WorkItem = Object.assign({}, this.capture.getCurrentWorkItem());
    const currentLineItems = new MetaDataLineItems();
    currentLineItems.push(...this.lineItems);
    const item = currentLineItems[itemIndex];

    if (!item) {
      if (counter > 10) {
        this.hideSpinner();
        return;
      }

      this.addItem().then(() => this.setFieldValue(itemIndex, mdd, value, moveNext, ++counter, addNewLineItem));
      this.hideSpinner();
      return;
    }

    const md = item.get(mdd.name);
    if (!md) {
      this.hideSpinner();
      return;
    }

    md.validity = MetaDataValidity.unknown;

    if (mdd.type === MetaDataType.Lookup) {
      this.hideSpinner();
      return;
    }

    this.showSpinner('Indexing.validating-items');
    return this.capture
      .validateLineItemValue(mdd, value, true)
      .then((metadata: IMetaData) => {
        item.set(mdd.name, metadata.value as string, metadata.validity);
        this.showSpinner('Indexing.saving-items');
        return this.capture
          .saveLineItems(currentLineItems, currentWorkItem)
          .then(() => {
            if (currentWorkItem.id === this.capture.getCurrentWorkItem().id && moveNext) {
              return this.moveToNextCell().then(() => {
                this.hideSpinner();
              });
            } else if (addNewLineItem) {
              return this.addItem(true).then(() => {
                this.hideSpinner();
              });
            } else {
              this.hideSpinner();
            }
          }, this.hideSpinner)
          .catch((err) => {
            console.log(err);
            this.hideSpinner();
          })
          .then(this.hideSpinner);
      }, this.hideSpinner)
      .catch((err) => {
        console.log(err);
        this.hideSpinner();
      })
      .then(this.hideSpinner)
      .catch(this.hideSpinner);
  }

  shouldValueBeSet(rowIndex: number, cellIndex: number): string {
    if (rowIndex === this.selectedRowIndex && cellIndex === this.selectedCellIndex) return this.value;

    return '';
  }

  mapGroupOfValues(values: Array<string>) {
    const currentSelectedRowIndex = this.selectedRowIndex;
    const existingItems = this.lineItems.slice(currentSelectedRowIndex);

    if (existingItems.length < values.length) {
      this.showSpinner('Indexing.loading-line-items-defaults');

      this.getLineItemDefaults()
        .then((defaultLineItem: MetaDataCollectionDictionary) => {
          this.hideSpinner();

          for (let i = existingItems.length; i < values.length; i++) {
            this.lineItems.push(new MetaDataCollection(defaultLineItem));
          }

          this.updateLineItemsReference();

          this.applyColumnMetaDataValues(values, currentSelectedRowIndex);
        })
        .catch((err) => {
          this.hideSpinner();

          this.toastr.error(this.translate.instant('Indexing.line-items-defaults-load-error'));
        });
    } else {
      this.applyColumnMetaDataValues(values, currentSelectedRowIndex);
    }
  }

  applyColumnMetaDataValues = (values: Array<string>, oldSelectedRowIndex: number): void => {
    const promises = [];
    for (let i = 0; i < values.length; i++) {
      promises.push(
        this.setFieldValue(
          this.selectedRowIndex + i,
          new MetaDataDefinition(this.lineItemMDD[this.selectedCellIndex]),
          values[i],
          false
        )
      );
    }

    Promise.all(promises).then(async () => {
      this.value = values[0];
      if (this.lineItemMDD[this.selectedCellIndex].type !== MetaDataType.Lookup) {
        this.selectedRowIndex = oldSelectedRowIndex;
        await this.moveToNextCell();
      }

      this.cdr.detectChanges();
    });
  };

  getValidity(item: MetaDataCollection, mdd: IMetaDataDefinition): MetaDataValidity {
    if (!item) {
      return null;
    }

    const md = item.get(mdd.name);
    if (!md) {
      return null;
    }

    return md.validity;
  }

  onScroll() {
    this.dropDown.scrolled();
  }

  onSaveLookupValues(event: SaveLookupsEvent, index: number) {
    const item = this.lineItems[index];

    event.lookups.forEach((lookup) => {
      if (!lookup.overwrite) {
        if (item.get(lookup.metadata.name).value) {
          return;
        }
      }

      item.set(lookup.metadata.name, lookup.value, lookup.validity);
    });

    this.showSpinner('Indexing.saving-items');
    this.hydraApi.validateAndUpdateBulkMetadata(
      new BulkValidationRequest(
        item.serializable(),
        event.lookups.map((x) => x.metaDataId),
        true
      ),
      (mdc: MetaDataCollection) => this.onRowValidationSucceed(mdc, event, index),
      this.onRowValidationFailed
    );
  }

  onRowValidationSucceed = (mdc: MetaDataCollection, event: SaveLookupsEvent, index: number): void => {
    this.lineItems[index] = mdc;
    this.updateLineItemsReference();

    this.capture.saveLineItems(this.lineItems).then(() => {
      if (event.move) {
        if (event.forward) {
          this.moveToNextCell().then(() => this.hideSpinner());
        } else {
          this.moveToPreviousCell();
          this.hideSpinner();
        }
      } else {
        this.hideSpinner();
      }
    });
  };

  onRowValidationFailed = (err: any): void => {
    this.hideSpinner();
    Log.error('LineItemIndexingComponent.onRowValidationFailed', err);
  };

  getContext(): any {
    return this.capture.getCurrentWorkItem();
  }

  showTotalsRow = (): boolean => {
    if (!this.lineItemMDD) return false;

    for (let i = 0; i < this.lineItemMDD.length; i++) {
      if (this.canCalculateTotal(i)) return true;
    }

    return false;
  };

  canCalculateTotal = (cellIndex: number): boolean => {
    if (!this.lineItems || this.lineItems.length == 0) return false;
    if (!this.lineItemMDD || cellIndex >= this.lineItemMDD.length || cellIndex < 0) return false;

    const mdd = this.lineItemMDD[cellIndex];

    if (!mdd) return false;
    if (this.allowedTypesForTotals.findIndex((x) => x == mdd.type) == -1) return false;

    const lic = this.getLineItemConfigById(mdd.id);
    if (!lic.showTotal) return false;

    return true;
  };

  getLineItemConfigById = (metaDataId: string): LineItemConfiguration => {
    const mdd = this.userStateService.findMetaDataById(this.lineItemMetaDataId);

    if (!mdd) return null;
    if (!(mdd as MetaDataDefinition).lineItemConfigurations) return null;

    return (mdd as MetaDataDefinition).lineItemConfigurations.find((x) => x.metaDataId == metaDataId);
  };

  getColumnTotalText = (cellIndex: number): string => {
    const total = this.getColumnTotal(cellIndex);
    let text = total.toString();

    if (this.hasCompareToField(cellIndex)) {
      const compareToValue = this.getCompareToValue(cellIndex);
      const op = total == compareToValue ? '=' : '<>';
      const compareToFieldName = this.getCompareToField(cellIndex).label;

      text += `   ${op} ${compareToFieldName} (${compareToValue})`;
    }

    return text;
  };

  getColumnTotal = (cellIndex: number): number => {
    if (!this.lineItems || !this.lineItemMDD || cellIndex >= this.lineItemMDD.length || cellIndex < 0) return 0;

    let total = 0;
    const mdd = this.lineItemMDD[cellIndex];
    this.lineItems.forEach((row) => {
      row.metadata.forEach((item) => {
        if (item.name != mdd.name.toLocaleLowerCase()) return;

        total += parseFloat(item.value.toString()) || 0;
      });
    });

    return total;
  };

  getCompareToValue = (cellIndex: number): number => {
    const mdd = this.getCompareToField(cellIndex);
    if (!mdd) return 0;

    const mdc = this.capture.getMetaDataCollection();
    if (!mdc) return 0;

    const md = mdc.get(mdd.name);
    if (!md || !md.value) return 0;

    return parseFloat(md.value.toString()) || 0;
  };

  hasCompareToField = (cellIndex: number): boolean => {
    return this.getCompareToField(cellIndex) != null;
  };

  getCompareToField = (cellIndex: number): IMetaDataDefinition => {
    if (!this.canCalculateTotal(cellIndex)) return null;

    const mdd = this.lineItemMDD[cellIndex];
    if (!mdd) return null;
    const lic = this.getLineItemConfigById(mdd.id);
    if (!lic) return null;

    return this.userStateService.findMetaDataById(lic.compareToMetaDataId);
  };

  trackLineItemById(index: number, item: MetaDataCollection): string {
    return item.id;
  }

  getFieldId = (id: string, row: number, cell: number): string => {
    return `${id}-${row}-${cell}`;
  };

  updateLineItemsReference() {
    const lineItemsCopy = [...this.lineItems];
    this.lineItems = new MetaDataLineItems();
    this.lineItems.push(...lineItemsCopy);
  }

  setVirtualScrollViewportStyles() {
    const lineItemIndexingWrapper = this.getLineItemIndexingElement();
    if (!lineItemIndexingWrapper) return;

    const tableWrapperElement = this.tableWrapper?.directiveRef.elementRef.nativeElement as HTMLElement;
    if (!tableWrapperElement) return;

    const { clientWidth, scrollWidth } = tableWrapperElement;

    const minimumLineItemsTableWidth = 900;
    const lineItemsHeaderHeight = 30;

    this.virtualScrollViewportStyles = {
      width: (clientWidth > minimumLineItemsTableWidth ? clientWidth : scrollWidth) + 'px',
      height: lineItemIndexingWrapper.clientHeight - 2 * lineItemsHeaderHeight + 'px',
    };
  }
}
