import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';

import { Subscription } from 'rxjs';

import { ContextMenuComponent, FieldDefinition, MetaDataDefinition } from 'shared-code-lib';
import { IDocument } from '../../../../blocks/models/document';
import { FolderItem } from '../../../../blocks/models/folder-item';
import { CapturedDocument } from '../../capture-models/document';
import { DocumentCaptureService } from '../../capture-services';
import { CaptureUpdateService } from '../../capture-services/capture-update.service';
import { SelectedItemsService } from '../../capture-services/selected-items.service';

@Component({
  selector: 'app-work-item',
  templateUrl: './work-item.component.html',
  styleUrls: ['./work-item.component.scss'],
})
export class WorkItemComponent implements OnInit, OnDestroy {
  @Input() folderItem: FolderItem;
  @Input() parentFolderId: string;
  @Input() stepMetadataDefinitions: Array<MetaDataDefinition> = [];
  @Input() stepMetadataDefinitionsFields: Array<FieldDefinition> = [];
  @Input() contextMenu: ContextMenuComponent;
  @Output() documentLoaded = new EventEmitter();

  currentDocument: CapturedDocument;
  document: IDocument;
  isDocumentSelected: boolean;

  subscriptions: Subscription;
  constructor(
    private capture: DocumentCaptureService,
    private captureUpdate: CaptureUpdateService,
    private selectedItemsService: SelectedItemsService
  ) {
    this.subscriptions = new Subscription();
  }

  ngOnInit() {
    this.currentDocument = this.capture.getCurrentDocument();
    this.subscriptions.add(this.captureUpdate.onChangeDocument.subscribe((doc) => (this.currentDocument = doc)));

    this.document = this.folderItem.documents[0];

    this.onChangedSelectedItems();
    this.subscriptions.add(this.selectedItemsService.onChangeSelectedItems.subscribe(this.onChangedSelectedItems));
  }

  onChangedSelectedItems = () => {
    this.isDocumentSelected = this.selectedItemsService.isItemSelected(
      this.folderItem.workItem.id,
      this.folderItem.workItem.workflowStepId
    );
  };

  ngOnDestroy() {
    this.subscriptions.unsubscribe();
  }

  trackDocumentId(index, item) {
    return item.id;
  }

  onDocumentLoaded() {
    this.documentLoaded.emit();
  }
}
