import { EventEmitter, Injectable } from '@angular/core';
import { Subscription } from 'rxjs';
import { AuthService, DelayService } from 'shared-code-lib';
import { LockAction, LockStatus } from '../../../blocks/models/lock-status';
import { HydraApiService } from '../../../blocks/services/hydra-api.service';
import { User } from '../../../blocks/services/models';
import { UserStateService } from '../../../blocks/services/user-state.service';
import { CapturedDocument } from '../capture-models';
import { CaptureUpdateService } from './capture-update.service';

@Injectable({
  providedIn: 'root',
})
export class DocumentLockingService {
  private static leaseRefreshIntervalSeconds = 60 * 1000;

  private lockStatuses = {};

  public lockStatusChanged = new EventEmitter<LockStatus>();
  private currentUser: User;

  subscriptions = new Subscription();

  constructor(
    private hydraApi: HydraApiService,
    private delayService: DelayService,
    private userStateService: UserStateService,
    private captureUpdateService: CaptureUpdateService,
    private authService: AuthService
  ) {
    delayService.execute(this.refreshLease, DocumentLockingService.leaseRefreshIntervalSeconds);

    this.subscriptions.add(captureUpdateService.onChangeDocument.subscribe(this.onDocumentChanged));
    this.subscriptions.add(userStateService.userChanged.subscribe(this.onUserChanged));
    this.subscriptions.add(authService.onLogout.subscribe(this.unlockDocuments));

    this.currentUser = userStateService.user;
  }

  private onDocumentChanged = (doc: CapturedDocument): void => {
    this.unlockDocuments();

    if (doc) {
      this.lock(doc.workitem.id);
    }
  };

  private onUserChanged = (user: User): void => {
    if (!user) return;

    this.unlockDocuments();
    this.currentUser = user;
  };

  public unlockDocuments = (): void => {
    if (!this.userStateService.isLoggedIn()) return;

    for (const documentId in this.lockStatuses) {
      const ls: LockStatus = this.lockStatuses[documentId];
      if (ls == null) continue;

      if (ls.lockedByUserId != this.currentUser.id) continue;
      if (!ls.locked) continue;

      this.unlock(documentId);
    }
  };

  public isLocked = (documentId: string): boolean => {
    const ls: LockStatus = this.lockStatuses[documentId];
    if (!ls) return false;

    return ls.locked && !ls.expired;
  };

  public isLockedByCurrentUser = (documentId: string): boolean => {
    if (!this.currentUser) return false;

    const ls: LockStatus = this.lockStatuses[documentId];
    if (!ls) return false;

    return ls.locked && ls.lockedByUserId == this.currentUser.id && !ls.expired();
  };

  public lock = (documentId: string): void => {
    this.lockStatuses[documentId] = null;
    this.hydraApi.setDocumentLock(LockAction.Lock, documentId, this.onDocumentLockedChanged, this.onDocumentLockFailed);
  };

  public unlock = (documentId: string): void => {
    this.hydraApi.setDocumentLock(
      LockAction.Unlock,
      documentId,
      this.onDocumentLockedChanged,
      this.onDocumentUnlockFailed
    );
  };

  public refreshLease = (): void => {
    if (!this.currentUser) return;

    for (const documentId in this.lockStatuses) {
      const ls: LockStatus = this.lockStatuses[documentId];
      if (ls == null) continue;

      if (ls.lockedByUserId != this.currentUser.id) continue;
      if (!ls.locked) continue;

      this.hydraApi.refreshLock(ls.id, this.onDocumentLockedChanged, this.onDocumentLockFailed);
    }

    this.delayService.execute(this.refreshLease, DocumentLockingService.leaseRefreshIntervalSeconds);
  };

  private onDocumentLockedChanged = (lockStatus: LockStatus): void => {
    this.lockStatuses[lockStatus.id] = lockStatus;

    this.lockStatusChanged.emit(lockStatus);
  };

  private onDocumentLockFailed = (err): void => {
    console.log(err);
    //toast: ?
  };

  private onDocumentUnlockFailed = (err): void => {
    //toast: ?
  };
}
