import {
  AfterViewInit,
  Component,
  ElementRef,
  EventEmitter,
  HostListener,
  Input,
  OnDestroy,
  OnInit,
  Output,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { PerfectScrollbarDirective } from 'ngx-perfect-scrollbar-portable';
import { ToastrService } from 'ngx-toastr';
import { Subject, Subscription } from 'rxjs';

import { PdfViewerComponent } from 'ng2-pdf-viewer';
import { Annotation, CalculationsService, DelayService } from 'shared-code-lib';
import { Action, ActionEvent } from '../../../../blocks/core/actions';
import { LockStatus } from '../../../../blocks/models/lock-status';
import { IWorkItem } from '../../../../blocks/models/work-item';
import { ActionService } from '../../../../blocks/services/action.service';
import { HydraApiService } from '../../../../blocks/services/hydra-api.service';
import { UserStateService } from '../../../../blocks/services/user-state.service';
import { DocumentCaptureService } from '../../../../views/document-capture/capture-services/document-capture.service';
import { Capabilities, CapturedDocument, DragData, Page, PageState } from '../../capture-models';
import { CaptureConstants } from '../../capture-models/capture-constants';
import { CaptureUpdateService } from '../../capture-services/capture-update.service';
import { DocumentLastTouchedService } from '../../capture-services/document-last-touched.service';
import { DocumentLockingService } from '../../capture-services/document-locking.service';
import { DocumentViewerService } from '../../capture-services/document-viewer.service';

declare const $: any;

@Component({
  selector: 'app-thumbnails',
  templateUrl: './thumbnails.component.html',
  styleUrls: ['./thumbnails.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class ThumbnailsComponent implements OnInit, OnDestroy, AfterViewInit {
  @Input() wrapper: ElementRef;
  @Input() annotationsAllowed: boolean = false;
  @Output() toggleSpinner = new EventEmitter<boolean>();
  @ViewChild('thumbnailsPanel', { static: true }) thumbnailsPanelElementRef: ElementRef;
  @ViewChild('thumbnailsUl', { static: true }) thumbnailsUl: ElementRef;
  @ViewChild('viewer') viewer: PdfViewerComponent;
  @ViewChild(PerfectScrollbarDirective, { static: true }) directiveRef?: PerfectScrollbarDirective;
  thumbnailsPanelElement: Element;

  currentDocument: CapturedDocument;
  selectedPage: Page;
  isPageDraggable: boolean;
  pageCanBeDeleted: boolean;
  subscriptions: Subscription;
  widthResizedSubject: Subject<number>;
  currentPage: Page;
  waitingPages: Page[] = [];

  documentLocked: boolean;

  pageString = CaptureConstants.page;

  @HostListener('window:resize')
  onResize() {
    this.widthResized();
  }

  constructor(
    private viewerService: DocumentViewerService,
    public capture: DocumentCaptureService,
    private captureUpdate: CaptureUpdateService,
    private actionService: ActionService,
    private hydraApi: HydraApiService,
    private toastr: ToastrService,
    private translate: TranslateService,
    private calculate: CalculationsService,
    private delay: DelayService,
    private documentLockingService: DocumentLockingService,
    private userStateService: UserStateService,
    private documentLastTouchedService: DocumentLastTouchedService
  ) {
    this.subscriptions = new Subscription();
    this.widthResizedSubject = new Subject<number>();
  }

  ngAfterViewInit() {
    this.thumbnailsPanelElement = this.thumbnailsPanelElementRef.nativeElement;
    this.thumbnailsPanelElement.addEventListener('ps-scroll-y', this.onPanelScrolled);
  }

  ngOnInit() {
    this.subscriptions.add(this.captureUpdate.onChangeDocumentPage.subscribe((page) => (this.selectedPage = page)));
    this.subscriptions.add(this.documentLockingService.lockStatusChanged.subscribe(this.lockStatusChanged));

    this.subscriptions.add(
      this.captureUpdate.onChangeDocument.subscribe((doc) => {
        this.documentLocked = false;

        this.delay.execute(() => {
          this.currentDocument = doc;
          this.thumbnailsPanelElement.scrollTop = 0;
          this.currentPage = null;
          this.waitingPages = [];

          if (doc) {
            setTimeout(() => {
              this.viewerService.thumbnailScrolled(0);
              this.widthResized();
            }, 50);
          }

          const ws = this.capture.getWorkflowStepById(this.capture.getCurrentStepId());
          this.isPageDraggable = ws && ws.hasCapability(Capabilities.allowDropDocumentPage);
          this.pageCanBeDeleted = ws && ws.hasCapability(Capabilities.deletePage);
        }, 10);
      })
    );

    this.subscriptions.add(this.actionService.onActivateAction.subscribe(this.invokeAction));

    window.addEventListener('resize', this.onPanelScrolled);

    this.subscriptions.add(
      this.captureUpdate.onRotatePage.subscribe(() => {
        this.capture.loadDocumentPage(this.selectedPage, () => {
          this.getThumbnail(this.selectedPage);
        });
      })
    );
  }

  lockStatusChanged = (ls: LockStatus): void => {
    if (!this.currentDocument || !this.currentDocument.workitem) return;

    if (ls.id == this.currentDocument.workitem.id) {
      this.documentLocked = ls.locked && ls.lockedByUserId == this.userStateService.user.id && !ls.expired();
    }
  };

  invokeAction = (event: ActionEvent): void => {
    if (event.action === Action.NextPage) {
      this.movePageSelection(1);
      return;
    } else if (event.action === Action.PreviousPage) {
      this.movePageSelection(-1);
      return;
    } else if (event.action === Action.BreakDocument) {
      this.breakDocument();
      return;
    }
  };

  movePageSelection(offset: number) {
    if (!this.selectedPage) return;

    let nextPageOrder = this.selectedPage.order + offset;

    if (nextPageOrder < 1) {
      nextPageOrder = this.currentDocument.pageCount;
    } else if (nextPageOrder > this.currentDocument.pageCount) {
      nextPageOrder = 1;
    }

    this.setSelectedPage(nextPageOrder);
  }

  breakDocument(): void {
    if (this.selectedPage.order < 2 || !this.isPageDraggable) return;
    if (!this.documentLocked) return;

    this.hydraApi.splitDocumentByPageNumber(
      this.capture.getCurrentWorkItem().id,
      this.currentDocument.id,
      this.selectedPage.order,
      (newWorkItem: IWorkItem) => {
        const documentId = newWorkItem.files[0];

        const parentFolder = this.capture.findFolder(this.currentDocument.parentFolderId);
        const searchResult = parentFolder.findDocument(documentId);
        if (searchResult) {
          this.capture.selectDocument(
            searchResult.document,
            searchResult.workItem,
            this.currentDocument.parentFolderId
          );
          return;
        }

        this.documentLastTouchedService.setLastTouchedWorkItem(newWorkItem.id);
      },
      (err) => {
        this.toastr.error(this.translate.instant('Document_Capture.document-break-failed'));
      }
    );
  }

  setSelectedPage(order: number) {
    this.selectedPage = this.currentDocument.pages.find((p) => p.order === order);
    this.capture.selectSpecificPage(this.selectedPage);

    const pageElementId = `page-${order - 1}`;
    const element = document.getElementById(pageElementId);
    const container = this.thumbnailsPanelElementRef.nativeElement;
    if (!this.calculate.isVisibleInScrollableElement(element, container, false)) {
      this.directiveRef.scrollToElement(`#${pageElementId}`, 0, 100);
    }
  }

  ngOnDestroy() {
    this.subscriptions.unsubscribe();

    window.removeEventListener('resize', this.onPanelScrolled);
    if (this.thumbnailsPanelElement) {
      this.thumbnailsPanelElement.removeEventListener('ps-scroll-y', this.onPanelScrolled);
    }
  }

  onSort(dragData: DragData) {
    this.toggleSpinner.emit(true);
    this.capture
      .handleSortingPages(dragData)
      .then(() => {
        this.capture.sortPages(true);
        this.toggleSpinner.emit(false);
      })
      .catch(() => {
        this.capture.sortPages(false);
        this.toggleSpinner.emit(false);
      });
  }

  onToggleSpinner(state: boolean) {
    if (state) {
      this.toggleSpinner.emit(true);
    } else {
      this.toggleSpinner.emit(false);
    }
  }

  onPanelScrolled = (ev?: Event): any => {
    this.viewerService.thumbnailScrolled(this.thumbnailsPanelElement.scrollTop);
  };

  onScroll(position: number) {
    this.directiveRef.scrollToY(position, 100);
  }

  widthResized() {
    const ulWidth = $(this.thumbnailsUl.nativeElement).width();
    this.waitingPages = [];
    this.widthResizedSubject.next(ulWidth);
    this.onPanelScrolled();
  }

  onPageLoad() {
    this.onPanelScrolled();
  }

  renderNextThumbnail() {
    if (this.capture.currentPage?.order) {
      this.waitingPages = this.waitingPages.sort((p1) => this.capture.currentPage.order - p1.order);
    }

    const nextThumbnail = this.waitingPages.splice(0, 1)[0];
    this.getThumbnail(nextThumbnail);
  }

  getThumbnail(page: Page) {
    if (!page || !page.viewerURL) return;

    const pageAlreadyHasThumbnailUrl = !!page.thumbnailURL;
    if (pageAlreadyHasThumbnailUrl) this.renderNextThumbnail();

    if (this.currentPage) {
      const isPageInQueue = this.waitingPages.findIndex((p) => p.id === page.id) !== -1;
      if (!isPageInQueue) this.waitingPages.push(page);
    } else {
      this.currentPage = page;
    }

    this.updateSize();
  }

  onRender() {
    if (!this.currentPage) {
      return;
    }

    const canvas = document.querySelector('.canvasWrapper canvas') as HTMLCanvasElement;

    this.waitingPages = this.waitingPages.filter((p) => p.id !== this.currentPage.id);

    this.currentPage.thumbnailURL = canvas.toDataURL('image/jpeg');
    this.currentPage.state = PageState.Loaded;
    this.currentPage = null;

    this.renderNextThumbnail();
  }

  onAnnotationsLoaded = () => {
    if (!this.annotationsAllowed) return;

    this.currentDocument.pages.forEach((page) => {
      page.onAnnotationsChanged();
    });
  };

  onAnnotationAdded = (annotation: Annotation) => {
    if (!this.annotationsAllowed) return;

    const page = this.currentDocument.pages.find((x) => x.order == annotation.pageNumber);
    if (!page) return;

    page.annotations.push(annotation);
    page.onAnnotationsChanged();
  };

  onAnnotationUpdated = (annotation: Annotation) => {
    if (!this.annotationsAllowed) return;

    const page = this.currentDocument.pages.find((x) => x.order == annotation.pageNumber);
    if (!page) return;

    const index = page.annotations.findIndex((x) => x.id == annotation.id);
    if (index == -1) return;
    page.annotations[index] = annotation;
    page.onAnnotationsChanged();
  };

  onAnnotationRemoved = (annotation: Annotation) => {
    if (!this.annotationsAllowed) return;

    const page = this.currentDocument.pages.find((x) => x.order == annotation.pageNumber);
    if (!page) return;

    const index = page.annotations.findIndex((x) => x.id == annotation.id);
    if (index == -1) return;
    page.annotations.splice(index, 1);
    page.onAnnotationsChanged();
  };

  updateSize() {
    if (this.viewer && this.viewer.page) {
      this.viewer.updateSize();
    }
  }
}
