import {
  AfterViewInit,
  Component,
  ElementRef,
  OnDestroy,
  OnInit,
  Renderer2,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';

import { TranslateService } from '@ngx-translate/core';
import { PerfectScrollbarDirective } from 'ngx-perfect-scrollbar-portable';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';

import { ContextMenuComponent, DelayService, Key, UserPreferencesService } from 'shared-code-lib';
import { Action, ActionEvent } from '../../../blocks/core/actions';
import { Log } from '../../../blocks/core/log';
import { ListHeaderOptions } from '../../../blocks/directives/list-header.directive';
import { FolderItem } from '../../../blocks/models/folder-item';
import { Workflow } from '../../../blocks/models/workflow';
import { WorkflowFolder } from '../../../blocks/models/workflow-folder';
import { ActionService } from '../../../blocks/services/action.service';
import { HydraApiService } from '../../../blocks/services/hydra-api.service';
import { DocumentListStyle } from '../capture-models';
import { CaptureUpdateService } from '../capture-services/capture-update.service';
import { DocumentCaptureService } from '../capture-services/document-capture.service';
import { DocumentListStyleService } from '../capture-services/document-list-style.service';

@Component({
  selector: 'app-documents-list',
  templateUrl: './documents-list.component.html',
  styleUrls: ['./documents-list.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class DocumentsListComponent implements OnInit, AfterViewInit, OnDestroy {
  @ViewChild('wrapper', { static: true }) wrapper: ElementRef;
  @ViewChild('scrollbar', { static: true }) scrollbar?: PerfectScrollbarDirective;
  @ViewChild('workflowPanel', { static: true }) panel: ElementRef;
  @ViewChild('listHeader') listHeader: ElementRef;

  private _currentWorkflow: Workflow;

  get currentWorkflow() {
    return this._currentWorkflow;
  }

  lastWorkFlowSelection: Workflow;
  loadingFoldersInProgress: boolean;
  folders: Array<WorkflowFolder>;
  listHeaderDirectiveOpts: ListHeaderOptions;
  subscriptions: Subscription;
  documentListStyle: DocumentListStyle;
  isGridView: boolean;
  contextMenu: ContextMenuComponent;

  readonly DocumentListStylePreference = 'DocumentListStylePreferenceId';

  constructor(
    public capture: DocumentCaptureService,
    private captureUpdate: CaptureUpdateService,
    private hydraApi: HydraApiService,
    private translate: TranslateService,
    private toastr: ToastrService,
    private actionService: ActionService,
    private delay: DelayService,
    private documentListStyleService: DocumentListStyleService,
    private renderer: Renderer2,
    private userPreferencesService: UserPreferencesService
  ) {
    this.subscriptions = new Subscription();
  }

  ngOnInit() {
    Log.enter('DocumentsListComponent.ngOnInit');

    this.renderer.listen(this.wrapper.nativeElement, 'contextmenu', (event) => {
      event.preventDefault();
    });

    try {
      this.isGridView = this.documentListStyleService.getStyle() === DocumentListStyle.Grid;
      this.subscriptions.add(
        this.captureUpdate.onChangeWorkflow.subscribe((workflow) => {
          const el = document.querySelector('#workflow-panel');
          if (el) {
            el.scrollTop = 0;
          }

          if (!this.loadingFoldersInProgress) {
            this._currentWorkflow = workflow;
            this.loadFolders();
          } else {
            this.lastWorkFlowSelection = workflow;
          }
        })
      );

      this.subscriptions.add(
        this.captureUpdate.onSelectNextDocument.subscribe(() => {
          this.selectNextDocument();
        })
      );

      this.subscriptions.add(
        this.captureUpdate.onScrollToDocument.subscribe((id: string) => {
          const documentElementID = `#doc-${id}`;
          const documentElement = document.querySelector(documentElementID);

          this.scrollbar.update();
          this.delay.execute(() => {
            this.scrollbar.scrollToElement(documentElementID, -100);
          }, 50);
        })
      );

      this.subscriptions.add(this.actionService.onActivateAction.subscribe(this.invokeAction));
      this.subscriptions.add(
        this.captureUpdate.onChangeFolder.subscribe((folder) => {
          if (folder) {
            this.focusPanel();
          }
        })
      );

      this.subscriptions.add(this.scrollbar.psScrollDown.subscribe(() => this.contextMenu.close()));
      this.subscriptions.add(this.scrollbar.psScrollUp.subscribe(() => this.contextMenu.close()));

      this.actionService.activateAction(new ActionEvent(Action.SetFocusTreePanel));
    } catch (e) {
      Log.error('DocumentsListComponent.ngOnInit', e);
    }
  }

  ngAfterViewInit() {
    this.listHeaderDirectiveOpts = {
      scrollbar: this.scrollbar,
      listWrapper: this.panel,
      disabled: this.documentListStyleService.getStyle() === DocumentListStyle.Tree,
    };
  }

  invokeAction = (event: ActionEvent): void => {
    if (event.action === Action.SetFocusTreePanel) {
      this.focusPanel();
      return;
    }
  };

  ngOnDestroy() {
    this.subscriptions.unsubscribe();
  }

  onRenderContextMenu(menu: ContextMenuComponent): void {
    this.contextMenu = menu;
  }

  loadFolders(): void {
    if (this._currentWorkflow) {
      this.folders = null;
      this.loadingFoldersInProgress = true;
      this.hydraApi.getWorkflowFolders(
        this._currentWorkflow.id,
        this.loadFolderViewCallback,
        this.loadFolderViewFailed
      );
    }
  }

  loadFolderViewCallback = (folders: Array<WorkflowFolder>) => {
    if (this.lastWorkFlowSelection && this.lastWorkFlowSelection.id !== this._currentWorkflow.id) {
      this._currentWorkflow = this.lastWorkFlowSelection;
      this.loadFolders();
    } else {
      this.folders = folders;
      this.capture.setWorkflowFirstFolder(this.folders[0]);
      this.loadingFoldersInProgress = false;
      this.lastWorkFlowSelection = null;
    }
  };

  loadFolderViewFailed = () => {
    this.toastr.error(this.translate.instant('Common.LoadingDataError'));
  };

  onScroll(position: number) {
    this.scrollbar.scrollToY(position, 100);
  }

  selectNextDocument = (): void => {
    const items = new Array<any>();

    this.getFlatList(items, this.folders);
    const index = this.getCurrentSelection(items);
    if (index === -1) {
      return;
    }

    for (let next = index + 1; next < items.length; next++) {
      if (items[next] instanceof FolderItem) {
        const folderItem = items[next] as FolderItem;
        this.capture.selectDocument(folderItem.documents[0], folderItem.workItem, folderItem.parentId);
        return;
      }
    }

    for (let next = 0; next < index && next < items.length; next++) {
      if (items[next] instanceof FolderItem) {
        const folderItem = items[next] as FolderItem;
        this.capture.selectDocument(folderItem.documents[0], folderItem.workItem, folderItem.parentId);
        return;
      }
    }
  };

  getPageSize = (): number => {
    return 12;
  };

  expandFolder = (expand: boolean): void => {
    const currentFolder = this.capture.getSelectedFolder();
    if (currentFolder == null) {
      return;
    }

    if (expand) {
      this.captureUpdate.expandFolder(currentFolder);
    } else {
      this.captureUpdate.collapseFolder(currentFolder);
    }
  };

  moveSelection = (offset: number): void => {
    const items = new Array<any>();

    this.getFlatList(items, this.folders);
    const index = this.getCurrentSelection(items);
    if (index === -1) {
      return;
    }

    let newIndex = index + offset;
    if (newIndex >= items.length) {
      newIndex = items.length - 1;
    }

    if (newIndex < 0) {
      newIndex = 0;
    }

    if (newIndex > items.length) {
      return;
    }

    if (newIndex === index) {
      return;
    }

    if (items[newIndex] instanceof WorkflowFolder) {
      this.capture.selectFolder(<WorkflowFolder>items[newIndex]);
    } else if (items[newIndex] instanceof FolderItem) {
      const folderItem = items[newIndex] as FolderItem;
      this.capture.selectDocument(folderItem.documents[0], folderItem.workItem, folderItem.parentId);
    }
  };

  getCurrentSelection = (items: Array<any>): number => {
    const currentFolder = this.capture.getSelectedFolder();
    const currentDocument = this.capture.getCurrentDocument();

    for (let index = 0; index < items.length; index++) {
      if (currentFolder) {
        if (!(items[index] instanceof WorkflowFolder)) {
          continue;
        }

        if (currentFolder.id === items[index].id) {
          return index;
        }
      } else if (currentDocument) {
        if (!(items[index] instanceof FolderItem)) {
          continue;
        }

        if (currentDocument.id === items[index].documents[0].id) {
          return index;
        }
      }
    }

    return -1;
  };

  getFlatList = (items: Array<any>, folders: Array<WorkflowFolder>) => {
    for (const folder of folders) {
      items.push(folder);

      if (folder.expanded) {
        if (folder.items) {
          for (const item of folder.items) {
            if (!item.documents) {
              continue;
            }

            items.push(item);
          }
        }

        if (folder.folders) {
          this.getFlatList(items, folder.folders);
        }
      }
    }
  };

  isDocumentVisible = (element: Element): boolean => {
    const panelElement = document.querySelector('#workflow-panel');
    return (
      panelElement &&
      element &&
      (<any>element).offsetTop > panelElement.scrollTop &&
      (<any>element).offsetTop + (<any>element).clientHeight < panelElement.scrollTop + panelElement.clientHeight
    );
  };

  onKeyDown = (event: KeyboardEvent): void => {
    if (event.ctrlKey || event.altKey || event.shiftKey) {
      return;
    }

    switch (event.key) {
      case Key.ArrowUp:
        this.moveSelection(-1);
        break;
      case Key.ArrowDown:
        this.moveSelection(1);
        break;
      case Key.ArrowLeft:
        this.expandFolder(false);
        break;
      case Key.ArrowRight:
        this.expandFolder(true);
        break;
      case Key.PageUp:
        this.moveSelection(-1 * this.getPageSize());
        break;
      case Key.PageDown:
        this.moveSelection(this.getPageSize());
        break;
      case Key.Home:
        this.moveSelection(-1000000);
        break;
      case Key.End:
        this.moveSelection(1000000);
    }
  };

  getDocumentListStyle = (): DocumentListStyle => {
    return this.documentListStyleService.getStyle();
  };

  changeDocumentListStyle = (): void => {
    (document.activeElement as HTMLElement)?.blur();

    const newStyle =
      this.documentListStyleService.getStyle() === DocumentListStyle.Tree
        ? DocumentListStyle.Grid
        : DocumentListStyle.Tree;
    this.documentListStyleService.update(newStyle);
    this.userPreferencesService.setPreference(this.DocumentListStylePreference, newStyle);
    this.isGridView = this.documentListStyleService.getStyle() === DocumentListStyle.Grid;

    if (newStyle === DocumentListStyle.Grid) {
      this.listHeaderDirectiveOpts.disabled = false;
      this.listHeaderDirectiveOpts = { ...this.listHeaderDirectiveOpts };
    } else {
      this.listHeaderDirectiveOpts.disabled = true;
      this.listHeaderDirectiveOpts = { ...this.listHeaderDirectiveOpts };
    }
  };

  getViewIconTextId() {
    return this.isGridView ? 'Document_Capture.ChangeToDocumentGridView' : 'Document_Capture.ChangeToDocumentTreeView';
  }

  focusPanel() {
    this.delay.execute(() => {
      this.panel.nativeElement.focus();
    }, 50);
  }

  trackFolderId(index, item) {
    return item.id;
  }

  onDocumentLoaded() {
    this.scrollbar.update();
  }
}
