import {
  ChangeDetectorRef,
  Component,
  EventEmitter,
  OnDestroy,
  OnInit,
  Output,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { PerfectScrollbarComponent } from 'ngx-perfect-scrollbar-portable';
import { Subscription } from 'rxjs';

import {
  BlurEvent,
  DelayService,
  DropDownService,
  FieldDataSource,
  FieldDefinition,
  MetaDataDefinition,
  MetaDataPreviewHilight,
  MetaDataType,
  MetaDataValidity,
  SaveLookupsEvent,
  StickyFieldsService,
} from 'shared-code-lib';
import { LockStatus } from '../../../blocks/models/lock-status';
import { WorkflowFolder } from '../../../blocks/models/workflow-folder';
import { HydraApiService } from '../../../blocks/services/hydra-api.service';
import { UserStateService } from '../../../blocks/services/user-state.service';
import { CapturedDocument, IndexingPanels, ValueLocation, ValueObj } from '../capture-models';
import { DocumentCaptureService, IndexingPanelService } from '../capture-services';
import { CaptureUpdateService } from '../capture-services/capture-update.service';
import { DocumentLockingService } from '../capture-services/document-locking.service';
import { SelectedItemsService } from '../capture-services/selected-items.service';

@Component({
  selector: 'app-indexing-panel',
  templateUrl: './indexing-panel.component.html',
  styleUrls: ['./indexing-panel.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class IndexingPanelComponent implements OnInit, OnDestroy {
  @ViewChild('perfectScroll') perfectScroll: PerfectScrollbarComponent;
  @Output() setCurrentField = new EventEmitter<string>();

  indexingPanel = IndexingPanels['AdvancedIndexing'];
  selectedDocument: CapturedDocument;
  selectedFolder: WorkflowFolder;
  fields: Array<MetaDataDefinition> = [];
  currentField: MetaDataDefinition;
  currentFieldIndex: number;
  value: string;
  subscriptions: Subscription;

  documentLocked: boolean;
  oneDocumentSelected: boolean;
  hasSelectedItems: boolean;
  private shouldFocusFirstField = true;

  timeout: number;

  disableBlur = false;

  get getContext() {
    return this.selectedDocument?.workitem;
  }

  constructor(
    private indexingService: IndexingPanelService,
    public capture: DocumentCaptureService,
    private captureUpdate: CaptureUpdateService,
    private hydraApi: HydraApiService,
    private stickyService: StickyFieldsService,
    private dropDown: DropDownService,
    private documentLockingService: DocumentLockingService,
    public userStateService: UserStateService,
    private delay: DelayService,
    private selectedItemsService: SelectedItemsService,
    private documentCaptureService: DocumentCaptureService,
    private cdr: ChangeDetectorRef
  ) {
    this.subscriptions = new Subscription();
  }

  ngOnInit() {
    this.subscriptions.add(
      this.captureUpdate.onChangeDocument.subscribe((doc) => {
        this.currentField = null;
        this.currentFieldIndex = null;
        this.documentLocked = false;
        this.value = null;

        this.disableBlur = true;
        this.selectedDocument = doc;

        this.shouldFocusFirstField = true;
        if (this.selectedDocument) {
          const valuesWithValidLocation: Array<ValueLocation> = [];

          this.fields.forEach((field) => {
            const fieldId = field.id;
            const location = this.capture.getValueLocation(field);
            if (location && location.valid) {
              const validity = this.capture.getFieldValidity(field);
              valuesWithValidLocation.push({ fieldId, location, validity });
            }
          });

          this.selectedDocument.workitem.metadata.metadata.forEach((md) => {
            console.log(md);

            if (md.previewHilight == MetaDataPreviewHilight.None) return;
            if (!md.location) return;
            if (!md.location.valid) return;

            valuesWithValidLocation.push({ fieldId: md.name, location: md.location, validity: md.validity });
          });

          this.captureUpdate.hasValuesWithValidLocation(valuesWithValidLocation);
        } else {
          this.currentField = null;
          this.currentFieldIndex = null;
        }

        const el = document.querySelector('#indexing-panel');
        if (el) el.scrollTop = 0;

        this.cdr.detectChanges();
        this.perfectScroll.directiveRef.scrollToTop();
        if (this.timeout) window.clearTimeout(this.timeout);
        this.timeout = window.setTimeout(() => {
          this.disableBlur = false;
        }, 100);
      })
    );

    this.subscriptions.add(this.documentLockingService.lockStatusChanged.subscribe(this.lockStatusChanged));

    this.subscriptions.add(
      this.indexingService.onChangeWorkflowStep.subscribe((metaDataArr) => {
        this.fields = metaDataArr.filter((x) => x.allowedForWorkflow(this.documentCaptureService.currentWorkflow.id));

        this.perfectScroll.directiveRef.update();
        this.perfectScroll.directiveRef.scrollToTop();
      })
    );

    this.subscriptions.add(
      this.captureUpdate.onChangeFolder.subscribe((folder) => {
        this.selectedFolder = folder;
      })
    );

    this.subscriptions.add(this.indexingService.onTakeFocus.subscribe(this.onTakeFocusHandler));

    this.subscriptions.add(
      this.indexingService.onPointAndClickInitialized.subscribe(() => {
        if (this.shouldFocusFirstField) {
          this.init();
          this.shouldFocusFirstField = false;
        }
        if (this.currentField) {
          this.indexingService.changeCurrentField(this.indexingPanel, this.currentField.name);
        }
      })
    );

    this.subscriptions.add(
      this.indexingService.onChangeCurrentFieldValue.subscribe((valueObj: ValueObj) => {
        if (valueObj.panel === this.indexingPanel) {
          if (this.selectedDocument && this.fields.length) {
            this.value = valueObj.updateFieldData.value;
            if (this.currentField.type !== MetaDataType.Lookup) {
              this.delay.execute(() => {
                this.moveTo(null);
              }, 100);
            }
          }
        }
      })
    );

    this.onChangedSelectedItems();
    this.subscriptions.add(this.selectedItemsService.onChangeSelectedItems.subscribe(this.onChangedSelectedItems));
  }

  onTakeFocusHandler = (panel: number) => {
    this.currentField = null;
    if (panel !== this.indexingPanel) return;

    this.moveTo(this.getFocusFieldIndex());
  };

  getFocusFieldIndex(): number {
    const fieldName = this.indexingService.getCurrentFieldName();
    const currentFieldIndex = this.fields.findIndex((field) => field.name === fieldName);
    return fieldName && currentFieldIndex !== -1 ? currentFieldIndex : 0;
  }

  onChangedSelectedItems = () => {
    this.oneDocumentSelected = this.selectedItemsService.isOneDocumentSelected();
    this.hasSelectedItems = this.selectedItemsService.hasItemsSelected();
  };

  lockStatusChanged = (ls: LockStatus): void => {
    if (!this.selectedDocument || !this.selectedDocument.workitem) return;

    if (ls.id == this.selectedDocument.workitem.id) {
      this.documentLocked = ls.locked && ls.lockedByUserId == this.userStateService.user.id && !ls.expired();
    }
  };

  ngOnDestroy() {
    this.subscriptions.unsubscribe();
  }

  init() {
    this.moveTo(0, false);
  }

  moveTo(fieldIndex: number | null, zoom?: boolean) {
    this.value = '';

    if (fieldIndex !== null) {
      this.currentFieldIndex = fieldIndex;
      this.currentField = this.fields[fieldIndex];

      if (zoom !== false) {
        const location = this.capture.getValueLocation(this.currentField);
        if (location && location.valid) {
          this.captureUpdate.zoomIntoValue(this.currentField.id);
        } else {
          this.captureUpdate.zoomIntoValue(null);
        }
      }
    } else {
      if (this.currentFieldIndex === this.fields.length - 1) {
        this.captureUpdate.selectNextDocument();
      } else {
        this.moveTo(this.currentFieldIndex + 1);
      }

      return;
    }

    if (this.currentField) {
      this.indexingService.changeCurrentField(this.indexingPanel, this.currentField.name);
    }
  }

  getFieldDefinition = (mdd: MetaDataDefinition): FieldDefinition => {
    const fd = mdd.asFieldDefinition();
    fd.datasource = FieldDataSource.create(mdd, this.hydraApi);

    return fd;
  };

  getFieldValue = (mdd: MetaDataDefinition): string => {
    if (this.disableBlur) return;
    if (!this.oneDocumentSelected || !this.selectedDocument) return '';

    const mddValue = this.capture.getWorkItemIndexingValue(mdd.name);
    if (!mdd.stickyField) return mddValue;

    if (mddValue) {
      this.stickyService.setStickyField(mdd.name, mddValue);
      return mddValue;
    }

    const stickyValue = this.stickyService.getStickyField(mdd.name);
    if (!stickyValue) return '';

    this.capture.validateAndSaveIndexingValue(stickyValue, mdd, this.getContext);
    return stickyValue;
  };

  shouldValueBeSet = (mdd: MetaDataDefinition): string => {
    if (this.currentField) {
      if (mdd.id === this.currentField.id) {
        return this.value;
      }
    }

    return '';
  };

  onFocus(index: number): void {
    this.moveTo(index);
  }

  onBlur(blurEvent: BlurEvent, mdd: MetaDataDefinition): void {
    console.log('onBlur', event, mdd);
    if (this.disableBlur) return;
    if (blurEvent.context.id != this.getContext.id) return;

    if (blurEvent.mdd) mdd = blurEvent.mdd;

    this.capture.validateAndSaveIndexingValue(blurEvent.value, mdd, blurEvent.context, true);

    if (mdd.stickyField) this.stickyService.setStickyField(mdd.name, blurEvent.value);
  }

  onReturn(value: any, mdd: MetaDataDefinition) {
    this.capture.validateAndSaveIndexingValue(value, mdd, this.getContext);
    this.captureUpdate.changeValueValidity({ id: mdd.id, validity: MetaDataValidity.valid });
  }

  getValidity = (mdd: MetaDataDefinition) => {
    const validity = this.capture.getFieldValidity(mdd);
    return validity || MetaDataValidity.unknown;
  };

  onTab(event: BlurEvent, mdd: MetaDataDefinition, index: number) {
    if (index !== this.fields.length - 1) return;

    if (mdd.stickyField) this.stickyService.setStickyField(mdd.name, event.value);
    if (document.activeElement && (document.activeElement as any).blur) (document.activeElement as any).blur();
  }

  onTabKey(event: Event, index: number) {
    if (event) event.preventDefault();

    if (index === this.fields.length - 1 && this.documentCaptureService.canAutomaticallyAdvanceOnTab()) {
      this.captureUpdate.selectNextDocument();
    } else {
      this.moveTo(index + 1);
    }
  }

  onShiftTabKey(event: Event, index: number) {
    if (event) {
      event.preventDefault();
    }

    if (index === 0) {
      this.moveTo(this.fields.length - 1);
    } else {
      this.moveTo(index - 1);
    }
  }

  onScroll() {
    this.dropDown.scrolled();
  }

  onSaveLookupValues(event: SaveLookupsEvent, index: number) {
    this.capture.updateMetadataValues(event.context, event.lookups);

    if (event.move) {
      this.delay.execute(() => {
        if (event.forward) {
          this.onTabKey(null, index);
        } else {
          this.onShiftTabKey(null, index);
        }
      }, 500);
    }
  }

  trackByDocumentMetaDataField = (index: number, item: MetaDataDefinition) => {
    return (this.selectedDocument?.id ?? '') + item?.id;
  };
}
