import {
  AfterViewInit,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
  SimpleChanges,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';

import { TranslateService } from '@ngx-translate/core';
import { NgxSmartModalService } from 'ngx-smart-modal';
import { Observable, Subscription } from 'rxjs';

import { DelayService } from 'shared-code-lib';
import { Action, ActionEvent } from '../../../../../blocks/core/actions';
import { KeyboardActionMapping } from '../../../../../blocks/core/keyboardActionMapping';
import { ActionService } from '../../../../../blocks/services/action.service';
import { Page, PageState } from '../../../capture-models';
import { DocumentViewerService } from '../../../capture-services';
import { DocumentCaptureService } from '../../../capture-services/document-capture.service';

declare const $: any;

@Component({
  selector: 'app-thumbnail',
  templateUrl: './thumbnail.component.html',
  styleUrls: ['./thumbnail.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class ThumbnailComponent implements OnInit, AfterViewInit, OnChanges, OnDestroy {
  @ViewChild('thumbnailWrapper', { static: true }) wrapperElementRef: ElementRef;
  @Input() page: Page;
  @Input() canBeDeleted: boolean;
  @Input() isPageDraggable: boolean;
  @Input() onWidthResized: Observable<number>;
  @Input() annotationsAllowed: boolean = false;
  @Output() loaded = new EventEmitter();
  @Output() getThumbnail = new EventEmitter<Page>();
  @Output() toggleSpinner = new EventEmitter<boolean>();

  wrapperElement: Element;
  subscriptions = new Subscription();
  thumbnailDefaultHeightPercentage = 1.2;

  PageState = PageState;

  constructor(
    private capture: DocumentCaptureService,
    private ngxSmartModalService: NgxSmartModalService,
    private translate: TranslateService,
    viewer: DocumentViewerService,
    private actionService: ActionService,
    private delay: DelayService
  ) {
    this.subscriptions.add(viewer.onThumbnailScroll.subscribe(this.onThumbnailScrolled));
  }
  ngOnChanges(changes: SimpleChanges): void {
    if (changes.page && changes.page.currentValue)
      this.subscriptions.add(this.page.annotationsChanged.subscribe(() => this.drawAnnotations()));
  }

  ngOnInit() {
    this.subscriptions.add(
      this.onWidthResized.subscribe(() => {
        this.drawAnnotations();
        this.getThumbnail.emit(this.page);
      })
    );
  }

  ngAfterViewInit() {
    this.wrapperElement = this.wrapperElementRef.nativeElement;
  }

  ngOnDestroy() {
    this.subscriptions.unsubscribe();
  }

  onThumbnailScrolled = (scrollTop: number): void => {
    if (!this.isPageInViewPort(this.wrapperElementRef, false)) {
      if (this.page.state !== PageState.Loaded) this.page.state = PageState.Empty;
      this.capture.cancelLoadDocumentPage(this.page);
      return;
    }

    if (this.page.state !== PageState.Empty) return;

    this.delay.execute(() => {
      if (!this.isPageInViewPort(this.wrapperElementRef, false) || this.page.state !== PageState.Empty) return;
      this.capture.loadDocumentPage(this.page, () => {
        this.getThumbnail.emit(this.page);
      });
    }, 400);
  };

  isPageInViewPort(element: ElementRef, fullyInView: boolean) {
    const pageTop = $(window).scrollTop();
    const pageBottom = pageTop + $(window).height();
    const elementTop = $(element.nativeElement).offset().top;
    const elementBottom = elementTop + $(element.nativeElement).height();

    if (fullyInView === true) {
      return pageTop < elementTop && pageBottom > elementBottom;
    } else {
      return elementTop <= pageBottom && elementBottom >= pageTop;
    }
  }

  select() {
    this.capture.selectSpecificPage(this.page);
  }

  confirmDelete(e: Event): void {
    e.stopPropagation();
    this.ngxSmartModalService.setModalData(
      {
        title: 'MainHeader.DeleteConfirmation',
        text: this.translate.instant('Document_Capture.Confirm_Delete_Page'),
        buttonText: this.translate.instant('Common.Delete'),
        confirm: this.confirmedDelete,
        cancel: this.cancelDelete,
      },
      'actionConfirmModal',
      true
    );

    this.ngxSmartModalService.getModal('actionConfirmModal').open();
  }

  confirmedDelete = () => {
    this.ngxSmartModalService.getModal('actionConfirmModal').close();
    this.toggleSpinner.emit(true);
    this.capture.deletePage(this.page, () => {
      this.toggleSpinner.emit(false);
    });
  };

  cancelDelete = () => {
    this.ngxSmartModalService.getModal('actionConfirmModal').close();
  };

  getBreakIconTitle(): string {
    const title = this.translate.instant('document-actions.break-document');
    if (this.capture.currentPage.id !== this.page.id) return title;

    const keyboardTooltip = KeyboardActionMapping.getTooltipFor(Action.BreakDocument, this.translate);
    return `${title}${keyboardTooltip}`;
  }

  breakDocument(): void {
    this.select();
    this.actionService.activateAction(new ActionEvent(Action.BreakDocument));
  }

  drawAnnotations() {
    if (!this.annotationsAllowed || !this.wrapperElementRef || !this.page || !this.page.annotations) return;

    const canvas = this.wrapperElementRef.nativeElement.getElementsByTagName('canvas')[0];
    if (!canvas) return;
    const img = canvas.parentElement.getElementsByTagName('img')[0];
    if (!img) return;

    canvas.width = img.width;
    canvas.height = img.height;
    const ctx = canvas.getContext('2d');
    ctx.clearRect(0, 0, canvas.width, canvas.height);

    this.page.annotations
      .sort((a, b) => a.order - b.order)
      .forEach((annotation) => {
        annotation.draw(ctx, canvas.width, canvas.height, false);
      });
  }
}
