import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Subscription } from 'rxjs';
import { LockStatus } from '../../../blocks/models/lock-status';
import { UserStateService } from '../../../blocks/services/user-state.service';
import { CapturedDocument } from '../capture-models';
import { CaptureUpdateService } from '../capture-services/capture-update.service';
import { DocumentLockingService } from '../capture-services/document-locking.service';
import { SelectedItemsService } from '../capture-services/selected-items.service';

@Component({
  selector: 'app-document-locked',
  templateUrl: './document-locked.component.html',
  styleUrls: ['./document-locked.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class DocumentLockedComponent implements OnInit {
  constructor(
    private documentLockingService: DocumentLockingService,
    private captureUpdateService: CaptureUpdateService,
    private userStateService: UserStateService,
    private translateService: TranslateService,
    private selectedItemsService: SelectedItemsService
  ) {}

  subscriptions = new Subscription();

  selectedDocument: CapturedDocument;
  lockStatus: LockStatus;
  lockedByUser: boolean;
  lockStatusText = '';
  oneItemSelected: boolean;

  ngOnInit() {
    this.subscriptions.add(this.captureUpdateService.onChangeDocument.subscribe(this.selectedDocumentChanged));
    this.subscriptions.add(this.documentLockingService.lockStatusChanged.subscribe(this.lockStatusChanged));

    this.onChangedSelectedItems();
    this.subscriptions.add(this.selectedItemsService.onChangeSelectedItems.subscribe(this.onChangedSelectedItems));
  }

  onChangedSelectedItems = () => {
    this.oneItemSelected = this.selectedItemsService.isOneItemSelected();
  };

  lockStatusChanged = (ls: LockStatus): void => {
    if (!this.selectedDocument || !this.selectedDocument.workitem) return;

    if (ls.id == this.selectedDocument.workitem.id) {
      this.lockStatus = ls;
      this.lockedByUser = ls.locked && ls.lockedByUserId == this.userStateService.user.id;

      if (!this.lockedByUser) {
        this.lockStatusText = `${this.translateService.instant('lock-status.locked-by-user')}: ${
          this.lockStatus.lockedByUserName
        } <${this.lockStatus.lockedByUserEmail}>`;
      } else {
        this.lockStatusText = this.translateService.instant('lock-status.locked-by-you');
      }
    }
  };

  selectedDocumentChanged = (doc: CapturedDocument): void => {
    this.lockedByUser = false;
    this.lockStatus = null;
    this.lockStatusText = '';
    this.selectedDocument = doc;
  };
}
