import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
import { DocumentListStyle } from '../capture-models/document-list-style';

@Injectable({
  providedIn: 'root',
})
export class DocumentListStyleService {
  documentListStyle: DocumentListStyle = DocumentListStyle.Tree;

  private _documentListStyleSubject = new Subject<DocumentListStyle>();
  onChangeDocumentListStyle = this._documentListStyleSubject.asObservable();

  update = (style: DocumentListStyle): void => {
    this.documentListStyle = style;
    this._documentListStyleSubject.next(style);
  };

  getStyle = (): DocumentListStyle => {
    return this.documentListStyle;
  };
}
