import { Injectable } from '@angular/core';

import * as signalR from '@microsoft/signalr';
import { Subject } from 'rxjs';

import { AuthService } from 'shared-code-lib';
import { HubConnectionOperations } from '../../../blocks/core/hubconnection-operations';
import { Log } from '../../../blocks/core/log';
import { FolderItem } from '../../../blocks/models/folder-item';
import { FolderView } from '../../../blocks/models/folder-view';
import { EventTypes, FolderViewEvent, ItemTypes } from '../../../blocks/models/folder-view-event';
import { WorkItem } from '../../../blocks/models/work-item';
import { Workflow } from '../../../blocks/models/workflow';
import { CapturedDocument } from '../capture-models';
import { DocumentsGridSortingConfig } from '../capture-models/sorting';
import { CaptureUpdateService } from './capture-update.service';
import { DocumentCaptureService } from './document-capture.service';

@Injectable({
  providedIn: 'root',
})
export class FolderViewEventsService {
  private _hubConnection: signalR.HubConnection;
  private _addFolderSubject = new Subject<FolderView>();
  private _deleteFolderSubject = new Subject<FolderView>();
  private _updateFolderSubject = new Subject<FolderView>();
  private _addItemSubject = new Subject<FolderItem>();
  private _deleteItemSubject = new Subject<FolderItem>();
  private _updateItemSubject = new Subject<FolderItem>();
  private _sortGridColumnData = new Subject<DocumentsGridSortingConfig>();
  private allowReconnect: boolean = true;

  onAddFolder = this._addFolderSubject.asObservable();
  onDeleteFolder = this._deleteFolderSubject.asObservable();
  onUpdateFolder = this._updateFolderSubject.asObservable();
  onAddItem = this._addItemSubject.asObservable();
  onDeleteItem = this._deleteItemSubject.asObservable();
  onUpdateItem = this._updateItemSubject.asObservable();
  onSortGridColumnData = this._sortGridColumnData.asObservable();

  constructor(
    private captureUpdate: CaptureUpdateService,
    private documentCaptureService: DocumentCaptureService,
    private authService: AuthService
  ) {
    if (documentCaptureService.currentWorkflow) {
      this.allowReconnect = true;
      this.connectFolderEvents(documentCaptureService.currentWorkflow);
    }

    this.captureUpdate.onChangeWorkflow.subscribe((workflow) => {
      if (workflow) {
        this.allowReconnect = true;
        this.connectFolderEvents(workflow);
      }
    });
  }

  connectFolderEvents(workflow: Workflow) {
    try {
      if (!this.allowReconnect) return;

      if (this._hubConnection && this._hubConnection.state === signalR.HubConnectionState.Connected) {
        this.allowReconnect = false;
        this._hubConnection.stop().then(() => {
          delete this._hubConnection;
          this.startFolderEvents(workflow);
        });
      } else {
        this.startFolderEvents(workflow);
      }
    } catch (e) {
      Log.error('FolderViewEventsService.connectFolderEvents', e);
    }
  }

  startFolderEvents(workflow: Workflow) {
    try {
      this._hubConnection = new signalR.HubConnectionBuilder()
        .withUrl('/eventhub', { accessTokenFactory: () => this.authService.getToken() })
        .build();
      HubConnectionOperations.setTimeout(this._hubConnection);
      this._hubConnection.start().catch((err) => console.log(err));

      this.allowReconnect = true;

      const notificationEvent = `folderview-${workflow.id}`;
      this._hubConnection.on(notificationEvent, (folderViewEvent: FolderViewEvent) => {
        if (folderViewEvent.eventType === EventTypes.added) {
          this.addEventItem(folderViewEvent);
        } else if (folderViewEvent.eventType === EventTypes.updated) {
          this.updateEventItem(folderViewEvent);
        } else if (folderViewEvent.eventType === EventTypes.deleted) {
          this.deleteEventItem(folderViewEvent);
        }
      });

      HubConnectionOperations.executeOnClose(this._hubConnection, () => {
        this.connectFolderEvents(workflow);
      });
    } catch (e) {
      Log.error('FolderViewEventsService.startFolderEvents', e);
    }
  }

  createEventFolderView(data: any) {
    return new FolderView(
      data.Id,
      data.TenantId,
      data.WorkflowId,
      data.WorkflowStepId,
      data.ParentId,
      data.Name,
      data.ItemCount
    );
  }

  addEventItem(folderViewEvent: FolderViewEvent) {
    if (folderViewEvent.itemType === ItemTypes.folder) {
      this.addFolderView(this.createEventFolderView(folderViewEvent.data));
    } else if (folderViewEvent.itemType === ItemTypes.item) {
      this.addFolderItem(
        new FolderItem(
          folderViewEvent.data.parentId,
          new WorkItem(folderViewEvent.data.workItem),
          folderViewEvent.data.documents
        )
      );
    }
  }

  updateEventItem(folderViewEvent: FolderViewEvent) {
    if (folderViewEvent.itemType === ItemTypes.folder) {
      this.updateFolderView(this.createEventFolderView(folderViewEvent.data));
    } else if (folderViewEvent.itemType === ItemTypes.item) {
      this.updateFolderItem(
        new FolderItem(
          folderViewEvent.data.parentId,
          new WorkItem(folderViewEvent.data.workItem),
          folderViewEvent.data.documents
        )
      );
    }
  }

  deleteEventItem(folderViewEvent: FolderViewEvent) {
    if (folderViewEvent.itemType === ItemTypes.folder) {
      this.deleteFolderView(this.createEventFolderView(folderViewEvent.data));
    } else if (folderViewEvent.itemType === ItemTypes.item) {
      this.deleteFolderItem(
        new FolderItem(
          folderViewEvent.data.parentId,
          new WorkItem(folderViewEvent.data.workItem),
          folderViewEvent.data.documents
        )
      );
    }
  }

  addFolderView(folderView: FolderView) {
    this._addFolderSubject.next(folderView);
  }

  updateFolderView(folderView: FolderView) {
    this._updateFolderSubject.next(folderView);
  }

  deleteFolderView(folderView: FolderView) {
    this._deleteFolderSubject.next(folderView);
  }

  addFolderItem(folderItem: FolderItem) {
    this._addItemSubject.next(folderItem);
  }

  updateFolderItem(folderItem: FolderItem) {
    this._updateItemSubject.next(folderItem);
    this.captureUpdate.updateWorkitemMetadata(folderItem.workItem, this.documentCaptureService.getCurrentWorkItem());
    this.checkParentFolderChange(folderItem);
  }

  deleteFolderItem(folderItem: FolderItem) {
    this._deleteItemSubject.next(folderItem);
  }

  sortGridColumnData(sortingConfig: DocumentsGridSortingConfig) {
    this._sortGridColumnData.next(sortingConfig);
  }

  checkParentFolderChange(fv: FolderItem) {
    const doc = this.documentCaptureService.getCurrentDocument();
    if (!doc || doc.workItemId != fv.workItem?.id) return;
    if (doc.parentFolderId == fv.parentId) return;

    const updatedDocument = new CapturedDocument(doc.document, fv.workItem, fv.parentId);
    this.captureUpdate.updateParentFolder(updatedDocument);
  }
}
